@import '../variables.scss';

.icon-button {
  cursor: default;
  font-size: 14px;
  position: relative;
  display: block;
  text-align: center;
  background: transparent;
  border: none;
  outline: none;
  &.blue,
  &.green,
  &.purple,
  &.orange,
  &.dark,
  &.light {
    cursor: pointer;
    padding: 7px 9px;
    margin: 0 3px;
    border-radius: 2px;
    color: white;
    &:active {
      box-shadow: inset 0 3px 15px 0 rgba(0, 0, 0, .3);
      transition: box-shadow 0.05s ease-out;
    }
  }
  &.light {
    color: $dark;
  }
  &.close-modal {
    font-size: 16px;
    cursor: pointer;
  }
  &.small {
    padding: 4px 6px;
  }
}

.icon-button-set {
  display: flex;
  flex-flow: row nowrap;
}
