@import '../variables.scss';

$hov: $lightblue;

.sketch-picker {
  padding: 0 !important;
  box-shadow: none !important;
  background: transparent !important;
  margin-bottom: -10px;
  input {
    text-align: center;
    width: 30px !important;
    &:focus {
      box-shadow: none;
      outline: 1px solid $lightblue;
    }
    + span{
      font-size: 9px !important;
      text-transform: uppercase !important;
      padding-top: 1px !important;
    }
  }
  > div:nth-of-type(3) {
    > div:first-of-type {
      input {
        width: 60px !important;
      }
    }
  }
  > div:nth-last-of-type {
    border-bottom: 1px solid $f4;
  }
  > div:last-of-type {
    border-top: 0 !important;
    padding-top: 27px !important;
    display: none !important;
  }
}

.ui-color {
  position: relative;
  width: $w;
  height: $w - 3px;

  .ui-color-swatch {
    position: relative;
    z-index: 1;
    border-radius: 2px;
    border: 1px solid $dd;
    color: white;
    width: 100%;
    height: 100%;
    cursor: pointer;
    transition: all 0.05s ease-out;
    //background: currentColor;
    background: white;
    &::before {
      display: flex;
      align-items: center;
      justify-content: center;
      font-family: 'icomoon-admin';
      content: "\e910";
      font-size:19px;
      color: color($dd shade(5%));
    }
    &:hover {
      background: $alice;
    }
    &.has-color {
      //border-color: transparent;
      &::before {
        color: inherit;
        content: "\e918";
      }
      &:hover {
        //background: currentColor;
        .ui-edit{
          display: flex;
        }
      }
    }
  }
  .ui-color-popover {
    position: relative;
    z-index: 2;
  }
  .ui-color-cover {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
  &::after {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    z-index: 0;
    background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQCAYAAAAf8/9hAAAAMElEQVQ4T2N89uzZfwY8QFJSEp80A+OoAcMiDP7//483HTx//hx/Ohg1gIFx6IcBALl+VXknOCvFAAAAAElFTkSuQmCC);
  }
}

.ui-color .ui-color-swatch.has-color[style="color: rgb(255, 255, 255);"] {
  border-color: $dd;
  &::before {
    text-shadow: 0 0 1px rgba(0, 0, 0, 0.8);
  }
}

.ui-color .ui-color-swatch.has-color[style="color: rgba(255, 255, 255, 0);"] {
  background: currentColor;
}

.ui-color .ui-color-swatch.has-color[style="color: rgba(0, 0, 0, 0);"] {
  background: currentColor;
}

.picker-backdrop {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
}

.ui-color-popover {
  z-index: 2999;
}

.text-shadow .ui-color-popover {
  min-width: 223px;
  .variables {
    min-width: 100%;
  }
}


.quick-edit-var{
  align-items: center;
  justify-content: center;
  font-size: 11px;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate3d(100%,0,0);
  -webkit-backface-visibility: hidden;
  background: $orange;
  color: white;
  border-radius: 0;
  width: 20px;
  height: 20px;
  z-index: 1;
  display: none;
  box-shadow: 10px 5px 50px 0 rgba(0,0,0,.7);
  i{
    transform: translate3d(0,0,0);
    -webkit-backface-visibility: hidden;
  }
  &:hover{
    background: $green;
    color: white;
  }
}

//body > div:last-of-type{
//  z-index: 9999;
//  position: relative;
//}

.ant-popover-placement-top > .ant-popover-content > .ant-popover-arrow{
  background: transparent;
  box-shadow: none;
}

html{
  --pw: 320px;
}

.variables {
  order:2;
  position: relative;
  padding-top: 7px;
  --gap: 6px;

  > div{
    position: relative;
    max-width: var(--pw);
    display: grid;
    gap: 0;
    grid-template-columns: repeat(auto-fill, minmax(42px, 1fr));
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: $c2;
    }
    &::-webkit-scrollbar-thumb {
      background: #555;
    }
    > div {
      color: transparent;
      height: 30px;
      position: relative;
      border-width: 0 calc(var(--gap) / 2) var(--gap) 0;
      border-color: #F6F8F9;
      border-style: solid;
      background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQCAYAAAAf8/9hAAAAMElEQVQ4T2N89uzZfwY8QFJSEp80A+OoAcMiDP7//483HTx//hx/Ohg1gIFx6IcBALl+VXknOCvFAAAAAElFTkSuQmCC);
      &.selected {
        z-index: 5;
        span {
          outline: 1px solid red;
          outline-offset: 1px;
          border-color: white !important;
          u{
            background-color: red;
            color: white;
            white-space: normal;
            font-size: 65%;
            width: 120%;
          }
        }
        span[style*="background-color: rgb(255, 255, 255);"]{
          border-color: black !important;
        }
      }
      > span {
        border: 1px solid $gray;
        width:100%;
        height: 100%;
        cursor: pointer;
        background-color: currentColor;
        position: relative;
        display: flex;
        align-items: flex-end;
        u{
          color: #777;
          background: #F6F8F9;
          font-family: Arial, sans-serif;
          text-decoration: none;
          letter-spacing: -0.2px;
          font-size: 60%;
          display: block;
          line-height: 0.9;
          position: absolute;
          text-align: center;
          left: 50%;
          top: calc(100% - 8px);
          transform: translate3d(-50%, 0, 0);
          padding: 2px;
          white-space: nowrap;
          overflow: hidden;
          width: 100%;
          pointer-events: none;
        }
      }
      &:hover{
        z-index: 5;
        span u{
          white-space: normal;
          color: white;
          background-color: red;
          font-size: 65%;
          width: 120%;
        }
      }
    }
  }

  > div > div{
    &:hover{
      .quick-edit-var{
        display: flex;
      }
    }
  }
}

.variables > div > div.selected > span u{
  background: red;
  color: white;
  border-color: white;
}

.ui-color-popover{
  .ant-popover-inner-content{
    > div{
      display:flex;
      flex-direction:column;
    }
    .sketch-picker{
      order:1
    }
    .color-fields{
      border-top: 1px solid #D6D9DE;
      order: 3;
      display: flex;
      align-items: center;
      margin-top: 8px;
      padding-top: 8px;
      position: relative;
      .colors-label{
        margin-right: auto;
        font-family: $sans;
        font-size: 13px;
        font-weight: 500;
        letter-spacing: -0.3px;
        line-height: 1.2;
        a{
          font-size: 90%;
          opacity: .6;
        }
      }
      .color-lightness,
      .color-alpha{
        display:flex;
        flex-direction:column;
        + div{
          margin-left: 10px;
        }
        label{
          line-height: 1;
          font-size: 10px;
          position: relative;
          opacity: 0.6;
          margin-bottom: 3px;
          font-weight: 600;
        }
      }
      input{
        width: 58px;
        margin-right: 2px;
        padding: 3px 5px;
        border: none;
        text-align: center;
        box-shadow: rgb(204, 204, 204) 0 0 0 1px inset;
        font-size: 13px;
        height: 34px;
        &:focus{
          outline: 1px solid #B2DBF4;
        }
        &::-webkit-inner-spin-button,
        &::-webkit-outer-spin-button{
          opacity: 1;
        }
      }
    }
    .variables{
      order:2;
      position: relative;
      border-top: 1px solid #D6D9DE;
      margin-top: 5px;
    }
    .color-variables{
      display: flex;
      align-items: center;
      justify-content: space-between;
      > span{
        font-family: $sans;
        text-transform: uppercase;
        font-size: 10px;
        position: relative;
        + span{
          display: flex;
          align-items: center;
          a{
            text-transform: uppercase;
            font-size: 10px;
          }
        }
      }
      div span{
        font-size: 80%;
        opacity: .7;
        letter-spacing: -.4px;
      }
    }
  }
}

.ui-color-popover .chrome-picker{
  box-shadow: none !important;
  background: transparent !important;
  width: var(--pw) !important;
  > div:first-child{
    padding-bottom: 0 !important;
    height: 140px;
    + div{
      padding: 0 !important;
      margin-top: 10px;
      > div:first-child{
        > div:first-child{
          > div{
            margin-top: 0 !important;
            width: auto !important;
            height: 100% !important;
            border-radius: 0 !important;
            margin-right: 4px;
            box-shadow: 2px 2px 0 0 rgba(0,0,0,.2);

          > div:first-child{
              border-radius: 0 !important;
              box-shadow: none !important;
            }
          }
          + div{
            margin-left: 5px;
          }
        }
      }
      > div + div{
        input{
          min-height: 30px;
        }
        label{
          margin-top: 7px !important;
          font-size: 12px !important;
        }
        svg{
          width: 30px !important;
          height: 30px !important;
        }
      }
    }
  }
  input + span{
    font-size: 10px !important;
    margin-top: 3px !important;
  }
}

.ui-color .ui-color-swatch.has-scheme{
  color: #a6a6a6;
  &::before {
    content: "\ead1";
    font-family: 'icomoon' !important;
    //font-weight: bold;
    color: inherit;
  }
}
span.scheme-icon{
  color: #a6a6a6;
  display: inline-flex;
  align-items: center;
  font-weight: normal;
  &::before{
    content: "\ead1";
    margin-right: 6px;
    font-size: 14px;
    font-family: 'icomoon' !important;
  }
}

.ant-popover-content .ui-select.scheme-select .Select {
  width: 100%;
}

.color-scheme-separator {
  padding: 0 10px;
}
