@import './variables.scss';

$pad2: 30px;


/******************************
 SubItem New Button
*******************************/

.tab-content {
  .module-items {
    .accordion-button {
      .accordion-heading {
        padding: 0;
        border-width: 0;
        &::before {
          display: none;
        }
      }
    }
    .accordion-heading{
      .new-item{
        justify-content: flex-start;
        font-size: 12px;
        background-color: color($blue);
        a{
          width: 100%;
        }
        span{
          i{
            top: 3px;
          }
        }
        &:hover{
          background-color: color($lightmarine shade(0%));
        }
      }
    }
    .module-items{
      .module-items{
        //display: none;
      }
      .accordion-heading{
        .new-item{
          background-color: color($blue shade(10%));
          span{
            visibility: hidden;
            &::before{
              visibility: visible;
              content: "Add";
              -webkit-font-smoothing: auto;
              font-family: inherit;
            }
            i{
              //margin-left:55px;
              top: 3px;
              visibility: visible;
            }
          }
          &:hover{
            background-color: color($lightmarine shade(10%));
          }
        }
      }
    }
  }
}

/******************************
 Mega Menu
*******************************/

$m: 15px;

.page-module{
  .position{
    margin-bottom:0;
    border-width: 0;
    border-radius: 0;
    min-height: 100%;
    background-color: $fa;
    //padding-top: 10px;
    .row-wrapper{
      border: 1px solid $bor;
      margin:10px 0 0 0;
      &:empty{
        //display: none;
      }
    }
    .new-item{
      width: calc(100% + 0px);
      justify-content: flex-start;
      color: white;
      a{
        color: inherit;
        width: 100%;
        display: flex;
        align-items: center;
        span{
          visibility: hidden;
          font-size: 12px;
          &::before{
            visibility: visible;
            content:"\e9d8";
            font-family: 'icomoon-admin';
            font-size: 17px;
            top: 1px;
            margin-left: 4px;
            position: relative;
          }
        }
      }
    }
  }
  .row-wrapper{
    background-color: $fa;
  }
  .position-heading{
    border-radius:inherit;
  }
  .outer-heading{
    //display: none;
  }
  .tab-content{
    > div{

    }
  }
}


.tab-content .module-items .accordion-button .accordion-heading{
  padding:6px !important;
}

div.new-item.button{
  border-radius:3px;
  height: 32px;
}

.page-module .position {
  .new-item.button{
    border-radius:0;
    height: 40px;
  }
}
