@import '../variables.scss';

$bg: $alice;

$mw: 60px;
$h: 29px;

.ui-input {
  input[value="a"] {
    text-transform: uppercase;
  }
}

.full-category{
  .ui-select,
  .ui-list-item{
    max-width: 100%;
    width: 100%;
    .Select {
      width: 100%;
    }
  }
}

.ui-input,
.ant-select-auto-complete.ant-select {
  position: relative;
  margin-right: 8px;
  display: inline-block;

  &.input-unit {
    display: flex;

    .ui-radio {
      margin-left: 10px;
    }
  }

  &:hover {
    .ui-clear.ui-edit {
      display: flex;
    }
  }

  input,
  .ant-input {
    line-height: 28px;
    padding: 0 8px;
    background: white;
    transition: all 0.1s ease-out;
    border-radius: 2px;
    border: 1px solid $dd;
    font-size: 12px;
    font-family: $sans;
    color: $dark;

    &:hover {
      background: $bg;
      border-color: $dd;
    }

    &:focus {
      background: $bg;
      outline: 0;
      border-color: $lightblue;
      box-shadow: none;
    }
  }

  &.input-large {
    input, textarea {
      width: 300px;
    }
  }

  &.input-number {
    input {
      width: $w !important;
      height: $h;
      padding: 0 3px;
      text-align: center;
    }
  }

  .ant-input {
    width: $mw;
  }

  &.input-numbers {
    input {
      width: $mw;
      height: $h;
      padding: 0 5px;
      text-align: center;
    }

    &:not(:first-of-type)::before {
      //content: "-";
      margin: 0 2px;
    }
  }

  &.labeled {
    margin-top: 7px;
  }

  span:not(:first-of-type) {
    margin-top: 7px;
  }

  input + img {
    margin-left: 7px;
  }
}

.input-full {
  .ui-text, .input-large {
    width: 100%;
    margin: 0;

    input, textarea {
      width: 100%;
    }
  }
}

.row-options .input-large,
.row-options .input-large input {
  width: 100%;
  margin: 0;
}

.ui-input.ui-input-number.input-number {
  .ant-input-number-input {
    min-height: 29px;
    //max-width: 52px;
    width: 39px !important;
    text-align: center;
  }
}

.ui-input.ui-input-number.input-numbers {
  .ant-input-number-input-wrap {
    input {
      width: 60px !important;
    }
  }
}

.input-number + .radio-number,
.input-number + .ui-color,
.input-number + .edit-btn,
.ui-multi + .ui-color {
  //margin-left: 8px;
}

.ui-input-lang {
  width: 100%;

  > div {
    display: flex;
    align-items: center;
    background: $e4;
    //max-width: 400px;
    + div {
      margin-top: 5px;
    }
  }

  .ui-input {
    margin-right: 0;
    width: 100%;

    input {
      width: 100% !important;
    }
  }
}

.ui-text-lang {
  width: auto;
  margin: -10px;
  flex-grow: 1;

  > div {
    align-items: flex-start;
  }

  .tab-container {
    width: 100%;
    //padding-bottom: 10px;
    //padding-right: 10px; interferes with block module content type Code
  }

  .ui-text textarea {
    width: 100%;
    height: 150px;
  }
}

.lang-flag {
  display: inline-block;
  width: 30px;
  height: 30px;
  position: relative;

  img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.ui-input:not(:first-of-type) {
  .input-label {
    margin-left: 7px;
  }
}

.input-label {
  display: block;
  position: absolute;
  width: 100%;
  text-align: center;
  top: -14px;
  opacity: .6;
  font-style: normal;
  font-size: 10px;
}

.ui-text {
  textarea {
    display: flex;
    padding: 5px 8px;
    width: 350px;
    height: 120px;
    line-height: 1.5;
    background: white;
    transition: all 0.1s ease-out;
    border-radius: 2px;
    border: 1px solid $dd;
    font-size: 12px;
    font-family: $sans;

    &:hover {
      background: $bg;
    }

    &:focus {
      background: $bg;
      outline: 0;
      border-color: $lightblue;
    }
  }
}

$pl: color($steel tint(40%));

::-webkit-input-placeholder {
  color: $pl;
  font-size: 11px;
}

::-moz-placeholder {
  color: $pl;
  font-size: 11px;
}

:-ms-input-placeholder {
  color: $pl;
  font-size: 11px;
}

/******************************
 inputValue
*******************************/

.ui-input-value,
.ant-select-auto-complete.ant-select .ant-input {
  height: $h;

  .ant-input {
    text-align: center;
  }
}

.ant-input:not(.ant-calendar-picker-input) {
  max-width: $mw;
}

.ui-input {
  .ant-input-number,
  .ant-select-focused .ant-select-selection,
  .ant-select-selection,
  .ant-select-selection:focus {
    border: 0;
    outline: 0;
    box-shadow: none;
  }
}

.ant-select-dropdown {
  width: auto !important;
  min-width: 100px;
  background: white;
  border-radius: 2px;
  border: 0;
  box-shadow: 0 5px 30px rgba(0, 0, 0, 0.2);
}

.ant-select-dropdown-menu-item {
  padding: 8px;
}

.ant-select-auto-complete.ant-select {
  margin-right: 0;
}

.ant-select-search.ant-select-search--inline {
  margin-top: 0;
}

/******************************
 inputNumber
*******************************/

.ant-input-number {
  width: auto;
  height: auto;
  background: transparent;
}

.ant-input-number-handler-up-inner,
.ant-input-number-handler-down-inner {
  transform: none;

  &::before {
    content: "\f106";
    font-family: FontAwesome !important;
    font-size: 12px;
  }
}

.ant-input-number-handler-down-inner:before {
  content: "\f107";
}

.ant-input-number-handler-down {
  top: 0;
}

.ant-input-number-handler-up,
.ant-input-number-handler-down {
  display: flex;
  align-items: center;
  justify-content: center;

  &:hover {
    height: 50% !important;
    background: $blue2;

    .ant-input-number-handler-up-inner,
    .ant-input-number-handler-down-inner {
      color: white;
    }
  }

  &.ant-input-number-handler-up-disabled,
  &.ant-input-number-handler-down-disabled {
    &:hover {
      background: color($orange tint(80%));
    }
  }
}

.ant-input-number-handler-wrap:hover .ant-input-number-handler {
  height: 50%;
}

.ant-input-number-handler-up-inner,
.ant-input-number-handler-down-inner {
  top: auto;
  margin-top: 0;
}

.ui-input span:not(:first-of-type),
.ant-select-auto-complete.ant-select span:not(:first-of-type) {
  margin-top: 0;
}

.ant-input-number-handler-wrap {
  opacity: 1;
  border: 1px solid $dd;
  border-radius: 0;
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
  height: $h;
  background: $f7;
}

.ant-input-number-input-wrap {
  padding-right: 21px;
}

.ant-input-number-focused {
  .ant-input-number-handler-wrap {
    border-color: $lightblue;
  }
}

.ant-select-selection {
  background-color: transparent !important;
}

.breakpoints .ui-input .ant-input {
  //width: 70px !important;
  min-width: 48px;
  letter-spacing: -0.5px;
  word-spacing: -3px;
  padding: 0 5px;
  background: aliceblue;
}

.ui-margin input {
  //text-transform: uppercase;
}

.ant-input-number-handler-up-inner svg, .ant-input-number-handler-down-inner svg {
  width: 9px;
  left: 50%;
  position: relative;
  transform: translateX(-50%);
}


.ui-input .ant-select-auto-complete.ant-select input,
.ui-input .ant-select-auto-complete.ant-select .ant-input {
  text-align: center;
  padding: 0 3px;
  width: 33px;
}

.ui-clear.ui-edit {
  z-index: 2;
  display: none;

  [class*="has-"]:hover & {
    display: flex;
  }
}

.ui-list {
  > .ui-input {
    .ant-select-auto-complete.ant-select .ant-input {
      border-color: lighten($purple, 18%);
    }

    .ui-clear.ui-edit {
      + .ant-select-auto-complete.ant-select .ant-input {
        //border-color: $purple2;
      }
    }
  }
}

.ant-select-selection__placeholder,
.ant-select-search__field__placeholder {
  z-index: 2;
  margin: 0 !important;
  cursor: text;
  position: absolute;
  width: 100%;
  left: 0;
  top: 55%;
  transform: translate3d(0, -50%, 0);
  text-align: center;
  font-size: 90%;
}

.input-autocomplete.input-numbers {
  input {
    min-width: 60px;
  }
}

.ant-select-dropdown-menu-item {
  min-width: 60px;
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid #ddd;

  span {
    line-height: 1;
    color: $dark;
    display: block;
    opacity: .5;
    font-size: 85%;
  }

  span + span {
    order: -1;
    //font-weight: bold;
    font-size: 12px;
    white-space: nowrap;
    margin-bottom: 3px;
    overflow: hidden;
    text-overflow: ellipsis;
    opacity: 1;
  }

  &.ant-select-dropdown-menu-item-selected {
    span + span {
      color: $purple2;
    }
  }
}

.ui-triple .ui-multi {
  display: flex;
  align-items: center;

  div {
    margin: 0 2px;

    &:first-child {
      margin-left: 0;
    }

    &:last-child {
      //margin-right: 8px;
      //max-width: 70px;
    }
  }

  .ui-input-icon-2 {
    opacity: .80;
    font-size: 80%;
    margin: 0 2px 0 1px;
    position: relative;
    top: -1px;
  }
}

.field-label {
  span small s {
    text-decoration: none;
    opacity: .85;
    font-size: 90%;

    &:last-of-type {
      font-size: 75%;
      position: relative;
      top: -1px;
    }
  }
}

.ui-triple-icon-1 {
  &::before {
    content: "";
  }
}

.ui-triple-icon-2 {
  &::before {
    content: "";
  }

  + .ui-input {
    max-width: 70px;
  }
}

.triple-only {

  .ui-radio {
    display: none;
  }

  .ui-triple {
    span {
      &::before {
        content: "";
      }
    }
  }
}

.ui-input-code {
  width: 100%;
}

.ui-pair.ui-triple + .breakpoints {
  margin-left: 5px;
}

// codemirror
.CodeMirror {
  resize: vertical;

  .CodeMirror-vscrollbar {
    margin-bottom: 17px;
  }

  .CodeMirror-hscrollbar {
    margin-right: 17px;
  }
}

.custom-gradient .ui-text textarea {
  width: 500px;
  height: 200px;
}

.ant-select-auto-complete.ant-select {
  width: 100%;
}

.font-size.input-number {
  width: 60px;
}


.input-number,
.input-numbers{
  input:not([value='']) {
    border-color: #b2b1b1 !important;
    //background: #f4f4f4 !important;
  }
}


/******************************
 Large variable gap inputs
*******************************/

//.ui-input.input-value .ant-select-auto-complete.ant-select .ant-input {
//  width: 190px;
//  max-width: 100%;
//  text-align: left;
//  padding-left: 5px;
//}


.input-gap,
.ui-margin,
.ui-padding,
.font-size {
  .ant-select-auto-complete.ant-select input {
    width: max-content !important;
    font-size: 12px;
    letter-spacing: -0.4px;
    word-spacing: -1px;
    min-width: 100%;
    padding: 0 4px !important;
    max-width: 60px;
  }

  .ui-edit + .ant-select-combobox input {
    &:not([value='']) {
      border-color: $green !important;
      background: #fcfffc !important;
    }
  }

  &.input-number {
    width: max-content;
  }

  .ant-select-combobox .ant-select-search__field__wrap {
    display: flex;
    flex-direction: column;
    width: max-content;

    input + .ant-select-search__field__mirror {
      position: relative;
      height: 0;
      font-size: 12px;
      letter-spacing: -0.2px;
      padding: 0 4px;
    }
  }

  .breakpoints .ui-input .ant-select-auto-complete.ant-select input {
    max-width: 60px;
  }
}

.ui-margin,
.ui-padding{
  .ant-select-auto-complete.ant-select input {
    max-width: 32px;
  }
}

.icon-popover{
  .field-option{
    flex-wrap: nowrap;
  }
}

li.ant-select-dropdown-menu-item[variable="-1"] {
  pointer-events: none;
  padding: 3px 8px;
  background: #d8dadc;
  > span:first-child{
    display: none;
  }
  > span + span{
    font-size: 9px;
  }
}
