@import '../variables.scss';

.font-preview {
  font-size: 22px;
  line-height: 22px;
  padding: 17px;
  background: white;
  border-bottom: 1px solid $dd;
  position: relative;
  a {
    cursor: pointer;
    font-size: 11px;
    text-decoration: underline;
    position: absolute;
    top: 0;
    right: 10px;
    &:hover {
      color: $green;
    }
  }
  input {
    width: 95%;
    border-color: $red;
    border-width: 1px;
    border-style: dashed;
    padding: 0 2px 3px 5px;
    margin: 0;
    &:enter {
      box-shadow: none;
      outline: 0;
      border-color: $green;
    }
  }
}

.font-editor-item {
  line-height: 34px;
  padding-left: 12px;
}

