  }
}

.g-fonts-link {
  position: absolute;
  right: -75%;
  text-decoration: underline;
  &:hover {
    color: $green;
  }
}

.ui-font {
  display: flex;
  .ui-select .Select {
    width: 210px;
  }
  > div {
    margin-right: 8px;
  }
  .edit-btn {
    margin-left: 0;
  }
}

.font-popover{
  z-index: 2999;
  max-width: 275px;
  .form-field-group {
    margin-bottom: 4px;
    .input-number input{
      padding: 0 3px;
    }
    .input-numbers input {
      min-width: 77px;
    }
  }
  .ant-popover-inner-content > div{
    display:flex;
    flex-wrap: wrap;
    > div:first-of-type .field-label{
      padding-top: 4px;
    }
  }
  .text-shadow .field-label{
    padding-bottom: 0;
  }
}

.ant-select-auto-complete.ant-select .ant-select-selection__rendered{
  line-height: 28px;
}

.global-font .ui-font > *:not(.ui-select+.ui-select) {
  display: none;
}
