@import '../variables.scss';

.button {
  color: white;
  cursor: pointer;
  padding: 6px 10px;
  position: relative;
  border-radius: 2px;
  font-size: 12px;
  transition: background 0.1s ease-out;
  background: transparent;
  border: none;
  i {
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
  }
  .fa-spin {
  }
  &.small {
    padding: 4px 8px;
    font-size: 11px;
  }
  &:hover,
  &:focus {
    color: white;
  }
  &:active {
    box-shadow: inset 0 3px 15px 0 rgba(0, 0, 0, .2);
    //box-shadow: inset 0 1px 15px -2px rgba(0, 0, 0, 0.6);
    transition: box-shadow 0.05s ease-out;
  }
  &::before {
    font-family: $fs;
  }

  + .button {
    //margin-left: 10px;
  }
}

.blue {
  background: $blue2;
  &:hover {
    background: color($blue2 tint(15%));
  }
}

.orange {
  background: $orange;
  &:hover {
    background: color($orange tint(15%));
  }
}

.red {
  background: $red;
  &:hover {
    background: color($red tint(15%));
  }
}

.purple {
  background: $purple;
  &:hover {
    background: color($purple tint(15%));
  }
}

.green {
  background: $green;
  &:hover {
    background: color($green tint(20%));
  }
}

.yellow {
  background: $yellow;
  &:hover {
    background: color($yellow tint(15%));
  }
}

.light {
  color: $dark;
  background: $dd;
  &:hover {
    background: color($dd tint(15%));
  }
}

.dark {
  background: $dark;
  &:hover {
    background: color($dark tint(15%));
  }
}

.save-button {
  background: $green;
  transition: all 0.1s ease-out;
  &:hover {
    background: $blue;
  }
}

.button.block {
  width: 100%;
}

.button-set {
  display: flex;
  flex-flow: row nowrap;
  > button:not(:last-of-type) {
  }
  .icon-clone {
    font-size: 11px;
  }
}

.edit-btn {
  width: 30px;
  height: $w - 4;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $quartz;
  &:hover,
  &.ant-popover-open {
    background: $blue;
    i {
      animation: fa-spin 2s infinite linear;
    }
  }
  i {
    font-size: 15px;
    margin-right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }
  &.has-changes {
    color: #27ae60;
  }
}

.reset-btn {
  background: $purple;
  &:hover {
    background: $yellow;
    i {
      animation: fa-spin 1s infinite linear;
    }
  }
}

.ui-icon-editor > span:only-of-type {
  .edit-btn {
    margin-left: 0;
  }
}

.quick-edit{
  background: $purple;
}

