@import '../variables.scss';

.ui-background{
  display: flex;
  > span > div{
    width: $w;
    background: white;
    margin-left:8px;
    cursor: pointer;
    border-radius: 2px;
    border: 1px solid $dd;
    overflow: hidden;
    &:hover{
      border-color: $steel;
    }
  }

  .bg-gradient {
    margin:0 8px;
  }

  .bg-image{
    width: $w;
    height: $w - 3;
    background-size: cover;
    background-repeat: repeat;
    background-position: center;
    border: 1px solid $dd;
    cursor: pointer;
    border-radius: 2px;
    position: relative;
    background-color: white;
    margin-right:8px;
    &:hover{
      border-color: color($dd shade(10%));
    }
    &::before{
      content: "\F030";
      font-family: 'icomoon-admin';
      font-size: 19px;
      color: $dd;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      top: 50%;
      transform: translate3d(0,-50%,0);
    }
    &.has-image{
      &::before{
        display: none;
      }
    }
  }
}


.has-gradient{
  .gradient-preview{
    border-color: red;
  }
  &:hover{
    .gradient-preview{
      &::before{
        color: white;
      }
    }
  }
}

.bg-image-popover{
  .bg-image-preview{
    img{
      max-width: 200px;
      height: auto;
      max-height: 200px;
    }
  }
}

.bg-popover{
  .form-field{
    flex-grow: 0;
  }
  .input-numbers{
    margin-right:8px;
  }
}

a.grad {
  display: inline-block;
  padding: 0 3px;
  border-radius: 2px;
  transition: all 0.15s ease-out;
  font-size: 11px;
  z-index: 2;
  text-decoration: underline;
}

.custom-gradient{
  position: relative;
  .sources{
    display: flex;
    align-items: center;
    justify-content: space-between;
    > div:first-child{
      text-transform: uppercase;
      font-size: 10px;
      position: relative;
      top: 2px;
    }
    span{
      font-size: 80%;
      opacity: .7;
      letter-spacing: -.4px;
    }
  }
  textarea{
    min-width: 450px;
  }
  + .variables{
    > div{
      display: grid;
      gap: 5px;
      grid-template-columns: repeat(auto-fill, minmax(40px, 1fr));
      margin: 0;
      max-width: 100%;
      > div{
        width: 100%;
        height: 35px;
        border: none;
        min-width: initial;
        max-width: 100%;
        span{
          border: none;
        }
        .gradient-preview{
          width: 100%;
          height: 100%;
          border: none;
        }
      }
    }
  }
}
.ui-gradient {
  .ui-clear.ui-edit{
    opacity: 0;
  }
  .bg-gradient{
    width: $w;
    height: $w - 3;
    position: relative;
    &:hover{
      .ui-clear.ui-edit{
        opacity: 1;
      }
    }
  }
}

.gradient-preview{
  width: 33px;
  height: 30px;
  position: absolute;
  border-radius: 2px;
  top: 0;
  left: 0;
  cursor: pointer;
  background: white;
  border: 1px solid $dd;
  &::before{
    content: "Gr";
    font-family: $sans;
    font-weight:bold;
    font-size: 18px;
    color: $dd;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height:100%;
  }
  &:hover{
    background: $alice;
  }
}

.gradient-big-preview{
  width: 100%;
  > div{
    width: 100%;
    min-height: 200px;
  }
}

.ui-icon-editor,
.ui-background {
  > .ui-checkbox{
    margin-left: 8px;
    label{
      min-width: 48px;
      border-radius: 2px !important;
      &.is-checked{
        background: $orange !important;
      }
    }
  }
}

.nobg .ui-background > *:nth-child(n+3){
  display: none;
}

.bg-popover .ant-popover-inner-content{
  > div{
    display: flex;
    flex-wrap: wrap;
    > div{
      .form-field:not(.field-single){
        min-height: 51px;
        .field-option{
          margin-top: 2px;
          flex-wrap: nowrap;
        }
      }
      .ui-input.input-numbers{
        margin-right: 2px;
      }
      &:first-of-type .field-label{
        padding-top: 4px;
      }
      > div:first-of-type .field-label{
        padding-top: 0;
      }
      > div + div{
        margin-top: 3px;
      }
    }
    > div + div{
      margin-left: 7px;
    }
  }
  .ui-select .Select{
    width: 185px;
  }
}

.bg-popover .ant-popover-inner-content > div > div div[data-name='overlay.blur']{
  display: flex;
  margin-top: 5px;
  .field-label{
    max-width: 80px;
  }
}
