@import "../variables";

.list-add-btn,
.list-remove-btn {
  width: 30px;
  height: calc(100% - 20px);
  color: white;
  background: $green;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.1s ease-out;
  cursor: pointer;
  font-size: 15px;
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 2px;
  &::before {
    content: "\e145";
    font-family: 'icomoon-admin';
    font-size: 21px;
    position: relative;
    top: -1px;
    transition: all 0.1s ease-out;
  }
  &:hover {
    background: $blue2;
    color: white;
  }
  &:active {
    box-shadow: inset 0 0 10px 0 rgba(0, 0, 0, .3);
  }
}

.not-available{
  background: $dd;
  cursor: default;
  &:hover{
    background: $dd;
  }
  &::before{
    content: "\e14b";
  }
}

.list-remove-btn {
  background: $orange;
  right: 0;
  font-size: 13px;
  height: 28px;
  //margin-top: -1px;
  position: relative;
  transform: translateY(0);
  &::before {
    content: "\eb85";
    position: relative;
    transform: translateY(1%);
  }
  &:hover {
    background: $blue2;
    color: white;
  }
  &:active {
    box-shadow: inset 0 0 10px 0 rgba(0, 0, 0, .3);
  }
}

.ui-list {
  ul {
    li {
      margin-top: 8px;
      position: relative;
      display: flex;
    }
  }
}

.ui-pair + .breakpoints {
  margin-left: 0;
}

.ui-list-item {
  display: flex;
  margin-right: 8px;
}

.breakpoints {
  display: flex;
  margin-left: 8px;
  .ui-input{
    display: inline-block !important;
  }
  > div:last-of-type {
    margin-right: 0;
  }
}

.ui-padding + .breakpoints,
.ui-margin + .breakpoints,
.ui-input + .breakpoints {
  margin-left: 0;
  position: relative;
}

.input-variable input{
  font-weight: bold;
}

