@import '../variables.scss';

$col: color($night tint(5%));
$open: color($night tint(5%));

.accordion-heading {
  cursor: pointer;
  margin: 0;
  background: $col;
  border-top: 1px solid color($col tint(15%));
  border-bottom: 1px solid color($col shade(20%));
  padding: 10px 25px;
  color: white;
  //transition: all 0.05s ease-out;
  min-height: 40px;
  position: relative;
  &:hover {
    background: color($col shade(5%));
  }
  &::before {
    content: "\f0da";
    font-family: $fs;
    position: absolute;
    font-size: 15px;
    left: 13px;
    top: 50%;
    transform: translateY(-50%);
  }
  &.is-sortable{
    padding: 10px 30px;
    &::before {
      left: 19px;
    }
  }
  span{
    font-size: 95%;
    opacity: .7;
  }
}


.accordion-open {
  > .accordion-heading {
    background: $open;
    border-color: color($open tint(15%));
    &::before {
      content: "\f0d7";
      left: 10px;
      color: #2CC690;
    }
    &.is-sortable{
      &::before {
        content: "\f0d7";
        left: 16px;
      }
    }
  }
}

.tab-content > .accordion-group > .accordion-item {
  &:first-of-type > .accordion-heading {
    border-top-color: $col;
  }
}

.tab-content > .accordion-group > .accordion-button > .accordion-heading {
  border-bottom-color: $col;
}

