.accordion-content {
  display: none;
  > div:not(.form-field) {
    > .form-field:first-child .field-option {
      border-top-width: 0;
    }
    > .form-field:last-child {
      border-bottom-width: 1px;
      .field-option {
        border-bottom-width: 0;
      }
    }
    + .form-field {
      border-top-width: 0;
    }
  }
  .form-field + div:not(.form-field) {
    > .form-field:first-child {
      border-top-width: 1px;
    }
  }
  .accordion-group {
    //background: $f4;
    //margin:10px 0 10px 10px;
    &:last-of-type {
      //margin-bottom: 0;
    }
  }
  .accordion-open > & {
    display: block;
  }
  .accordion-heading {
    background: $col2;
    border-top: 1px solid color($col2 tint(15%));
    border-bottom: 1px solid color($col2 shade(15%));
    &::before {
      transform: translateY(-53%);
    }
    &:hover {
      background: color($col2 shade(7%));
    }
  }
  .accordion-button {
    .accordion-heading {
      border-bottom: 0;
    }
  }
  .accordion-content {
    .accordion-heading {
      background: $col3;
      border-top: 1px solid color($col3 tint(15%));
      border-bottom: 1px solid color($col3 shade(10%));
      &:hover {
        background: color($col3 shade(5%));
      }
    }
    .accordion-content {
      .accordion-heading {
        background: $col4;
        border-top: 1px solid color($col4 tint(20%));
        border-bottom: 1px solid color($col4 shade(10%));
        &:hover {
          background: color($col4 shade(5%));
        }
      }
      .accordion-content {
        .accordion-heading {
          background: $col5;
          border-top: 1px solid color($col5 tint(20%));
          border-bottom: 1px solid color($col5 shade(10%));
          &:hover {
            background: color($col5 shade(5%));
          }
        }
        .accordion-content {
          .accordion-heading {
            background: $col6;
            border-top: 1px solid color($col6 tint(20%));
            border-bottom: 1px solid color($col6 shade(10%));
            &:hover {
              background: color($col6 shade(5%));
            }
          }
        }
      }
    }
    //.accordion-group{
    //  padding: 0 10px 10px 10px;
    //  margin-top: -10px;
    //  .field-option{
    //    margin-left: 0;
    //  }
    //}
  }
  .module-item a:hover{
    span{
      color: white;
    }
  }
  .option-group + .accordion-group .accordion-heading {
    border-top: 0;
  }
  .accordion-group {
    //margin:8px;

    .accordion-group {
      margin: 0;
    }

    .accordion-item:last-child {
      .accordion-heading {
        border-bottom-width: 0;
      }
    }
  }
  .accordion-open {
    > .accordion-heading {
      border-bottom-width: 0;
    }
  }
}

.accordion-item .accordion-content .form-field + div > .form-field:first-child {
  border-top-width: 0;
}

.accordion-content .accordion-open > .accordion-heading{
  //background: color($col2 shade(15%));
}

.accordion-buttons {
  background: $dark;
  .button {
    font-size: 9px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 4px 3px;
    color: #6c7f96;
    &:hover {
      color: white;
    }
    i {
      font-size: 12px;
      &::before {
        content: "\f107";
        margin-right: 2px;
      }
    }
    &.close-all {
      i::before {
        content: "\f106";
        position: relative;
        top: -1px;
      }
    }
  }
  ~ .accordion-item .accordion-buttons {
    display: none;
  }
  .page-variable &,
  .page-style &,
  .module-items &,
  .page-module-filter &,
  .edit-modal &{
    display: none !important;
  }
}

.grid-builder{
  .accordion-buttons{
    display: none;
  }
}

.no-buttons .accordion-buttons{
  display: none;
}

.accordion-heading{
  small{
    opacity: .7;
    display: inline-block;
    margin:0 5px;
  }
}

