@import '../variables.scss';

$bg: $quartz;
$tab: $night2;
$tabs: $oc;
$hov: color($tab shade(15%));

.tabs {
  //display: flex;
}

.j-content,
.ant-modal{
  ::-webkit-scrollbar {
    width: 4px;
    height: 4px;
  }
  ::-webkit-scrollbar-track {
    background: $c2;
  }
  ::-webkit-scrollbar-thumb {
    background: $blue;
  }
  .accordion-button{
    background: #36485E;
    &::after{
      display: none;
    }
  }
}

.tabs-vertical {
  > ul {
    width: $tw - 10;
    height: calc(100vh - 107px);
    position: fixed;
    overflow-y: auto;
    background: #293141;
    z-index: 2;
    //border-left: 8px solid transparent;
    padding-bottom:10px;
    //border-right: 10px solid transparent;
    &::before{
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      background: url("../assets/image/bg.jpg");
      opacity:.6;
    }
    > li {
      white-space: nowrap;
      text-overflow: ellipsis;
      > div:not(.button) {
        width: 100%;
        line-height: 1.3;
        span{
          display: block;
          width: calc(100% - 50px);
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
        .item-name{
          display: flex;
          align-items: center;
          flex-wrap: wrap;
        }
        small{
          display: block;
          font-size: 75%;
          opacity: .5;
          font-weight: 400;
          line-height: 1;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          max-width: 100%;
          //margin-bottom: 2px;
        }
      }
    }
  }
  > .tab-container {
    margin-left: $tw - 10;
    padding:8px;
    width: calc(100% - 170px);
    flex-grow: 1;
    background: $fa;
    min-height: 40px;
    > div:not(.tab-active) {
      display: none;
    }
    > .tab-content {
      //overflow-x: auto;
      //overflow-y: hidden;
      .module-items > div,
      .accordion-heading {
        position: relative;
      }
    }
  }
}

.tabs-vertical > .tab-container > .tab-content {
  min-height: calc(100vh - 223px);
  display:flex;
  flex-flow:column;
  background: $fa;
  width: 100%;
  > .form-field{
    flex-grow: 0;
  }
  li{
    small{
      opacity: 0.7;
      display: inline-block;
      margin: 0 5px;
    }
  }
}

.tab-items {
  color: white;
  padding: 0;
  margin: 0;
  list-style: none;
  position: relative;
  > li {
    background: $tab;
    cursor: pointer;
    margin: 0;
    border-top: 1px solid color($tab tint(8%));
    border-bottom: 1px solid color($tab shade(25%));
    color: color(white shade(20%));
    padding-left: 15px;
    height: 40px;
    position: relative;
    display: flex;
    align-items: center;
    &:first-of-type {
      border-top-color: #3b4b63;
      border-top-width: 0;
      &.tab-active::before {
        top: -1px;
      }
    }
    &:hover:not(.tab-disabled, .tab-active) {
      background: $hov;
      color: white;
    }
    &.tab-active, &.tab-active:hover {
      color: white;
      cursor: default;
      background: $hov;
      border-bottom: 1px solid color($tab shade(25%));
    }
    &:first-of-type {
      cursor: pointer;
      border-top-color: #54565d;
      &.tab-active {
        cursor: default;
      }
    }

    &.tab-active {
      &::before {
        content: "";
        width: 6px;
        height: calc(100% + 1px);
        background: $orange;
        position: absolute;
        left: 0;
        top: 0;
      }
    }

    &.tab-disabled {
      border-bottom: 0;
      padding: 10px;
      cursor: default;
      .button {
        width: 100%;
      }
    }
  }
}

/******************************
 Horizontal
*******************************/

$tb: white;
$box: inset 0 -8px 8px -6px rgba(0, 0, 0, .1);

.tabs-horizontal {
  display: flex;
  flex-flow: column nowrap;
  .tab-items {
    background: color($e2 shade(0%));
    box-shadow: $box;
    > li {
      background: color($e2 shade(5%));
      float: left;
      text-align: center;
      padding: 0 15px;
      height: 40px;
      font-size: 12px;
      line-height: 20px;
      color: $dark;
      cursor: pointer;
      border: 0;
      border-right: 1px solid #dcdcdc;
      box-shadow: $box;
      > span {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
      }

      &:hover:not(.tab-disabled) {
        background: color($e2 tint(30%));
        color:$dark
      }
      &.tab-active, &.tab-active:hover {
        color: $dark;
        background: $tb;
        box-shadow: none;
        border-bottom: 2px solid $green;
        .icon-button {
          color: #b6c4d6;
        }
      }
      &:first-of-type {
        cursor: pointer;
        &.tab-active {
          cursor: default;
        }
      }
      &.tab-active {
        &::before {
          display: none;
        }
      }
    }
  }
  .tab-container {
    border-left: none;
    margin-left: 0;
    background: $tb;
    width: 100%;
    .tab-content {
      padding: 10px 0 0 0;
      position: relative;
    }
    .form-field{
      //border-right-width: 1px;
      &:first-of-type {
        border-top-width: 0;
      }
    }
    .field-option{
      margin-left: 0;
    }
  }
  .reset-field{
    right: 10px;
  }
}

.field-option > .tabs-horizontal{
  width: 100%;
  width: -webkit-fill-available;
  width: -moz-available;
}
.ant-modal .field-option .tabs-horizontal .tab-container .tab-content{
  padding-left: 10px;
}

.tabs-horizontal .tab-container .form-field:first-of-type + div{
  border-top: 1px solid #e6e6e6;
}

$stb: color($fa shade(0%));


.sub-tabs, .ui-status{
  > .field-option {
    padding: 0;
    .tab-content > div{
      margin-bottom: 0;
    }
  }
  .tabs-horizontal{
    width: 100%;
    .tab-items{
      background: transparent;
      box-shadow: none;
      border-bottom: 1px solid $ee;
      > li{
        height: 30px;
        font-size: 11px;
        background:color($stb shade(4%));
        padding: 0 11px;
        //box-shadow: none;
        //box-shadow: inset 0 -8px 8px -4px rgba(0, 0, 0, 0.08);
        &.tab-active{
          background: $stb;
          border-bottom:0;
          &::before{
            content: "";
            width: 100%;
            height: 1px;
            background: $orange;
            position: absolute;
            left: 0;
            top: 0;
            //display: block;
          }
          &:hover{
            background-color: $stb;
          }
          &::after{
            content: "";
            width: 100%;
            height: 1px;
            background: $stb;
            position: absolute;
            left: 0;
            bottom: -1px;
          }
        }
        &:nth-child(2).tab-active{
          border-bottom:3px solid $orange;
        }
        &:nth-child(3).tab-active{
          border-bottom:3px solid $blue;
        }
      }
    }
    .tab-container{
      background: $stb;
      padding-left: 0 !important;
    }
    .tab-content{
      padding-bottom: 10px;
      padding-left: 10px;
      min-height: 50px;
      border-right: 1px solid $e2;
      .form-field{
        background-color: transparent;
        border: 0;
      }
      .field-option{
        border: 0;
        background-color: transparent;
      }
    }
  }
}

.tabs-horizontal.sub-tabs{
  > .tab-items{
    display: flex;
    align-items: center;
    overflow-x:auto;
    &::-webkit-scrollbar {
      -webkit-appearance: none;
    }
    &::-webkit-scrollbar:horizontal {
      height: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background-color: $blue;
      border-radius: 5px;
    }
    &::-webkit-scrollbar-track {
      background-color: #ddd;
    }
  }
  .tab-container .form-field:first-of-type + div {
    border-top: 1px solid #eee;
  }
}

.accordion-content > div > .tabs-horizontal > .tab-container > .tab-content{
  padding-left:0;
  padding-bottom:0;
}

.accordion-content .accordion-content > .tabs-horizontal > .tab-container > .tab-content{
  padding-left:0;
}

.accordion-content .tabs-horizontal .accordion-item:first-child .accordion-heading {
  border-top-width: 0;
}

.form-field:first-child .field-option {
  //border-top: 1px solid $e6;
}
.form-field:last-child .field-option {
  border-bottom: 1px solid $e6;
}

.page-skin .tabs-vertical > ul > li > div:not(.button) span{
  width:auto;
}

/******************************
 Language Tabs
*******************************/

.lang-field {
  .ui-input-lang{
    width: 100%;
    > div {
      align-items: flex-start;
      margin-bottom:0;
    }
  }
  .tabs-horizontal{
    .tab-container{
      width: 100%;
      .tab-content{
        display: table;
        width: 100%;
        .ui-image{
          float: left;
          > div{
            float: left;
          }
        }
      }
    }
  }
}

.tabs-horizontal{
  .lang-flag{
    min-width: 15px;
  }
  .field-label{
    max-width: 180px;
    flex: 0 0 180px;
  }
}

.ui-image-lang .tabs-horizontal .tab-container .tab-content{
  padding-bottom: 10px;
}

.ui-image-dimensions.sub-tabs{
  margin:-10px;
  flex: 1;
}

/******************************
 Item Status
*******************************/

i.fa-status-off{
  color:$orange !important;
  transform: scaleX(-1);
}

.tab-container .tab-container .tab-container{
  padding: 0 0 0 8px;
}


.tab-container .tab-container .tabs-horizontal .tab-container{
  padding-left: 0;
}

.tab-params {
  display: none !important;
}
