@import '../variables.scss';

.ui-variable {
  display: flex;

  .ui-create {
    margin-left: 8px;
    width: 29px;
    height: 29px;
    border-radius: 2px;
    background-color: #7091B7;
    cursor: pointer;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
      background: $blue;
    }

    .fa-plus {
      font-size: 20px;

      &::before {
        content: "\e145";
      }
    }
  }
}

.module-items > div:not(.tab-container) a > [class^="variable"] {
  overflow: visible;
  display: flex;
  align-items: center;
  justify-content: center;
}

.variable-label {
  margin-left: 5px;
  background: #37485e;
  padding: 1px 4px 2px;
  border-radius: 2px;
  font-size: 85%;
  order: 10;

  &:empty {
    display: none;
  }
}

.field-variable-name {
  .ui-input input {
    opacity: .6;

    &:hover {
      background: #fbe1e0;
      border-color: $orange;
    }
  }
}

.ipr {
  > .form-field:last-child {
    > .field-option {
      padding: 0;
      border-left: 1px solid #eeeeee;
    }
  }

  .items-per-row-section {
    width: 100%;
    max-width: 471px;
  }

  .form-field:last-child .field-option {
    border-bottom: 0;
  }
}

