@import "variables";

$bg: color($night tint(5%));
$separator: color(#2B323A tint(0%));

.tabbed-list {
  .tab-items > li {
    > div {
      width: 100%;
    }
    .item-name {
      display: block;
      width: calc(100% - 25px);
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      position: relative;
      top: -1px;
      color: $dd;
      height: 40px;
      line-height: 40px;
      small{
        font-size: 80%;
        display: inline-block;
        margin-left: 5px;
        opacity: .5;
      }
    }
  }
  .tab-content > div {
    &:empty {
      padding: 8px 15px;
      &::before {
        //content: "\f177";
        font-size: 30px;
        margin: 0 15px;
        position: relative;
        opacity: .5;
        top: 4px;
      }
    }
    &:empty::after {
      content: "Click + to create a module instance";
      font-family: $sans;
      font-size: 25px;
      font-weight: 300;
      position: absolute;
      text-align: center;
      top: 50%;
      left: 50%;
      margin-left: 90px;
      transform: translate(-50%, -50%);
    }
  }
}


.tab-items div:not(.module-icons) > .item-icon {
  position: absolute;
  top: 50%;
  right: 5px;
  transform: translateY(-50%);
  size: 21px;
  i {
    color: $dd;
    transition: all 0.05s ease-out;
  }
  &:hover i {
    color: $icons-hover;
  }
}

.module-icons {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  right: 5px;
  top: 50%;
  transform: translateY(-50%);
  padding-left: 2px;
  height: 100%;
  .item-icon{
    font-size: 16px;
    padding: 0 4px;
    min-width: 28px;
  }
}

.tab-items {
  > li {
    font-size: 13px;
    &:hover,
    &.tab-active {
      .module-icons {
        background: linear-gradient(to right, $night2 0%, $night2 25%);
        opacity: 1;
      }
    }

    &.separator {
      background-color: $separator;
      color: color($separator tint(50%));
      //color:#8DB4CF;
      border-top: 0;
      border-bottom-color: color($separator shade(10%));
      max-height: 20px;
      text-align: center;
      font-size: 10px;
      justify-content: center;
      padding: 0;
      line-height: 9px;
      pointer-events: none;

      + li {
        border-top: 0;
      }
    }

    &.new-module,
    &.legacy-module{
      > div > a {
        &::after {
          content: "New";
          background-color: #009688;
          padding: 0 4px 1px 3px;
          font-size: 75%;
          border-radius: 2px;
          margin-left: 5px;
        }
      }
    }
    &.legacy-module{
      > div > a{
        &::after{
          content: "Legacy";
          background-color: $blue;
        }
      }
    }
  }
}

.tab-items .separator ~ li{
  //padding-left: 15px;
}

.tab-items > li {
  .module-icons {
    background: $night2;
    //opacity:0;
  }
  &:hover,
  &.tab-active {
    .module-icons {
      opacity: 1;
      background: color($night2 shade(20%));
    }
  }
}

.item-icon {
  color: white;
  size: 1.5em;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin-left: 1px;
  font-size: 14px;
  .fa-clone {
    font-size: .88em;
  }
  &:hover {
    i {
      opacity: 1;
    }
  }
  i {
    position: relative;
    color: white;
    opacity: .5;
    padding: 1px;
  }
}

.icon-text {
  width: auto;
  margin-right: 10px;
  font-size: 12px;
  cursor: pointer;
  color: color($dd shade(5%));
  > i {
    color: white;
    font-size: 13px;
    &.fa-clone {
      font-size: 11px;
    }
  }
  > span {
    margin-left: 5px;
    display: inline-block;
  }
  &:hover {
    color: $icons-hover;
    i, span {
      color: inherit;
    }
  }
}

$mr: 8px;
$mbg: $dd;

.tab-content{
  .filters{
    margin-bottom: $mr;
  }
}

.filters {
  display: flex;
  align-items: center;
  background-color: $mbg;
  padding: $mr;
  > input{
    width: 15px;
    height: 15px;
    margin-left: 7px;
  }
  + .module-items .module-item{
    input[type="checkbox"] {
      margin: 1px 0 0 10px;
    }
  }

  > div:not(:first-child) {
    margin-left: 8px;
  }
  .filter {
    margin-right: auto;
  }
  .order .Select {
    max-width: 70px;
  }
  .limit .Select {
    max-width: 60px;
  }
  .sort .Select {
    max-width: 122px;
  }
}


.module-items {
  > div:not(.tab-container) {
    background: $bg;
    margin: 0;
    border-top: 1px solid color($bg tint(15%));;
    border-bottom: 1px solid color($bg shade(20%));
    transition: all 0.05s ease-out;
    min-height: 40px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:hover{
      background: color($bg shade(5%));
    }
    &.accordion-item{
      flex-direction: column;
      align-items: flex-start;
      width: 100%;
      > div{
        width: 100%;
      }
    }
    &:last-of-type {
      border-bottom-width: 0;
    }
    a {
      color: white;
      cursor: pointer;
      //opacity:.8;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      > span{
        min-width: 0;
        display: block;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
      &:hover {
        //color: $blue;
        opacity:1;
      }
    }
    > input[type="checkbox"] {
      margin-left: 10px;
    }
    > a {
      display: flex;
      align-items: center;
      min-height: 38px;
      flex-grow: 1;
      margin-left: 10px;
      margin-right: 80px;
    }
  }
  .form-field + .module-items{
    .accordion-item{
      border-width: 0;
    }
  }
}

.tab-container > .tab-content > .module-items > div:not(.accordion-button){
  border:0;
}
.module-items {
  > div:first-of-type {
    border-top: 0 !important;
  }
}

.module-item {
  display: flex;
  > div {
    height: 40px;
    flex-grow: 1;
    display: flex;
    align-items: center;

    > a {
      display: flex;
      margin-left: 5px;
      padding-left: 5px;
      height: 100%;
      align-items: center;
      width: 100%;
    }
  }
}


