@import '../variables.scss';

.ui-editor {
  width: 100%;
}
.ui-input-lang.ui-editor-lang {
  margin: -10px;
  flex-grow: 1;
}
.ui-input-lang{
  width: auto;
  .tabs-horizontal{
    .tab-items{
      width: 100%;
      background: #DCDFE2;
      > li{
        padding:0 12px;
        > div{
          span{
            display: flex;
            align-items: center;
            justify-content: center;
          }
        }
      }
    }
    .tab-container .tab-content{
      padding-bottom: 10px;
      padding-right: 10px;
    }
  }
}

.note-editor {
  .note-toolbar {
    border-bottom: 1px solid #CEC9C9;
  }
  .note-editable {
    background: white;
    min-height: 100px;
  }
  * {
    //max-width: 100%;
  }
}

.note-editor.note-frame{
  border: 1px solid #CEC9C9;
}

#modal-image {
  z-index: 10500;

  + .modal-backdrop {
    z-index: 10400;
  }
}


.ui-image-lang.ui-input-lang{
  margin: -10px;
  display: block;
  flex-grow: 1;
  > div{
    align-items: flex-start;
    .tab-items > li.tab-active,
    .tab-items > li.tab-active:hover{
      background: $fa;
    }
    .tab-container {
      width: 100%;
      background: $fa;
    }
  }
}

.ui-editor-lang{
  .tabs-horizontal .tab-container .tab-content{
    padding-left: 10px;
  }
}

.ui-input-lang.ui-text-lang{
  .tabs-horizontal .tab-container .tab-content{
    padding-left: 10px;
  }
}
