@import '../variables.scss';

$bg: $night;
$bg2: $green;
$rad: 2px;

.ui-checkbox {
  position: relative;
  > span {
    text-align: center;
    display: inline-block;
    vertical-align: middle;
    &:first-of-type {
      border-left: 0;
      label {
        border-left: 0;
        border-radius: $rad 0 0 $rad;
      }
    }
    &:last-of-type {
      border-right: 0;
      label {
        border-right: 0;
        border-radius: 0 $rad $rad 0;
      }
    }
  }
  input {
    display: none;
  }
  label {
    color: white;
    cursor: pointer;
    margin-bottom: 0;
    padding: 0 10px;
    min-height: 29px;
    position: relative;
    font-size: 11px;
    font-weight: normal;
    background: $bg;
    transition: all 0.05s;
    display: flex;
    min-width: 60px;
    justify-content: space-around;
    align-items: center;
    border-left: 1px solid color($bg tint(25%));
    border-right: 1px solid color($bg shade(15%));
    &:hover {
      background: color($bg tint(10%));
    }
  }
  .is-checked {
    background: $bg2 !important;
    box-shadow: inset 1px 2px 8px rgba(0, 0, 0, 0.3);
    color: white;
    border-left: 1px solid color($bg2 shade(5%));
    border-right: 1px solid color($bg2 shade(15%));
  }
}
