@import '../variables.scss';

.ui-checkbox-list {
  width: 100%;

  > div {
    display: flex;
    padding: 10px 0;

    &:first-child {
      padding-top: 0;
    }

    &:last-child {
      padding-bottom: 0;
    }

    &:not(:last-child) {
      border-bottom: 1px solid $e6;
    }

    > label {
      min-width: 300px;
      display: flex;
      align-items: flex-end;
    }
  }
}
