@import '../variables.scss';

.form-wrap {
  display: flex;
  .form-field {
    &:first-of-type {
      min-width: 420px;
      flex-grow: initial;
    }
    &:last-of-type {
      border-bottom-width: 1px;
    }
    &:not(:first-of-type) {
      background: $f4;
      .field-label {
        flex-basis: 70px;
      }
    }
  }
}

.scheme-separator{
  padding: 4px 8px;
  background: #adb6c1;
  font-size: 80%;
}

.add-img > .field-option {
  padding: 0;
  .tab-container{
    padding: 0 0 10px 10px;
  }
}

.form-divider {
  padding: 3px 0 4px 8px;
  background: $steel;
  color: white;
  font-size: 11px;
}

.text-stroke .ui-input{
  width: 100%;
}

.form-field {
  position: relative;
  border-width: 1px 1px 0 0;
  border-color: color($e6 tint(30%));
  border-style: solid;
  background: color($dd tint(20%));
  display: flex;
  flex-grow: 1;
  align-items: center;
  min-width: 700px;
  &:first-child {
    border-top-width: 0;
  }
  &:hover {
    //background: color($dd tint(15%));
    .field-option {
      //background: color($e2 tint(30%));
    }
    .field-label i {
      display: block;
    }
  }
  ul { // breaks ckeditor li display
    //list-style: none;
    //margin: 0;
    //padding: 0;
  }
  + .form-component {
    border-top: 1px solid color($e4 tint(30%));
  }
  &.field-single {
    border-color: color($e4 tint(30%));
  }
  .ui-list {
    width: 100%;
    ul{
      margin:0;
      padding:0
    }
  }
  .ui-clear{
    z-index: 2;
  }
}

.field-label {
  display: block;
  padding: 4px 8px 5px 8px;
  color: $dark;
  //overflow: hidden;
  text-overflow: ellipsis;
  //white-space: nowrap;
  line-height: 1.2;
  position: relative;
  font-size: 12px;
  max-width: 170px;
  flex: 0 0 170px;
  border-bottom: 0;
  a{
    font-size: 80%;
    display: inline-block;
    text-decoration: underline;
    text-decoration-style: dotted;
    position: relative;
    top: -1px;
    left: 3px;
  }
  span{
    a{
      left: 0;
    }
  }
  small {
    display: block;
    margin-top: 2px;
    opacity: .6;
    position: relative;
    font-size: 90%;
    white-space: normal;
    line-height: 11px;
  }
  > i {
    font-size: 12px;
    position: absolute;
    right: 0;
    display: none;
    color: $steel;
    top: 50%;
    transform: translateY(-50%);
    transition: all 0.1s ease-out;
    cursor: pointer;
    &:hover {
      color: $orange;
    }
  }
  .info-field {
    color: $blue;
    //display: block;
    transform: translateY(-110%);
    right: 1px;
    font-size: 13px;
    + .reset-field {
      transform: translateY(10%);
    }
  }
}

.tabs-horizontal .tab-container .field-label{
  .info-field{
    right: 11px;
  }
}

.ant-popover {
  .reset-field,
  .info-field {
    display: none !important;
  }
}

$bg: color($quartz tint(10%));

.field-option {
  margin-left: 10px;
  position: relative;
  padding: 10px;
  min-height: 50px;
  background: color($fa);
  flex-grow: 1;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  max-width: calc(100% - 180px); //codemirror max width needed
  .accordion-group {
    width: 100%;
    .accordion-heading {
      //color: $gray;
      background: $bg;
      border-top: 1px solid color($bg tint(20%));
      border-bottom: 1px solid color($bg shade(15%));
      padding: 8px 25px;
      min-height: 30px;
      transition: border-color 0s;
      &::before {
        transform: translateY(-50%);
      }
    }
    .accordion-open {
      .accordion-heading {
        border-bottom-color: $f4;
      }
    }
  }
  .field-single & {
    background: color($e2 tint(10%));
  }
}

input + div {
  z-index: 9;
  .option-font-editor & {
    padding: 0 !important;
  }
}

.group {
  display: flex;
  .form-field {
    flex: 1 0 100px;
    border-width: 0;
    border-left-width: 1px;
    .option-input {
      > div {
        display: block !important;
        input {
          width: 100%;
        }
      }
    }
  }
}

/******************************
 Language
*******************************/

.language-field {
  .nav-tabs {
    margin: 0;
    border: none;
    position: absolute;
    z-index: 2;
    right: 0;
    height: 28px;
    top: -28px;
    > li {
      margin: 0;
      padding: 0;
      > a {
        text-align: center;
        background: transparent;
        border-radius: 0;
        margin: 0;
        padding: 0;
        width: 38px;
        height: 28px;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 1px solid transparent;
        img {
          vertical-align: initial;
        }
      }
      &.active {
        > a {
          box-shadow: none;
          border: 1px solid #C5C5C5;
          border-bottom: 0;
          border-top: 0;
        }
      }
      &:last-of-type {
        > a {
          border-right: 0;
        }
      }
    }
  }
}

@media only screen and (max-width: 1025px) {
  .active + #content {
    .field-label {
      flex: 0 0 auto;
    }
    .form-field {
      flex-direction: column;
      align-items: flex-start;
      padding-bottom: 10px;
    }
    .field-option {
      width: calc(100% - 20px);
    }
  }
}

