/******************************
 POPOVER
*******************************/

$bg: $f6;

.ant-popover-placement-right,
.ant-popover-placement-rightTop,
.ant-popover-placement-rightBottom {
  padding-left: 8px;
}

.font-popover,
.bg-popover {
  .ant-popover-inner-content {
    background: $bg;
    padding: 10px;
  }
}

.font-popover{
  .form-field-group .field-option{
    display: block;
  }
}

.ant-popover-placement-right > .ant-popover-content > .ant-popover-arrow,
.ant-popover-placement-rightTop > .ant-popover-content > .ant-popover-arrow,
.ant-popover-placement-rightBottom > .ant-popover-content > .ant-popover-arrow {
  left: 0;
  border-right-color: $bg;
}

.ant-popover-inner-content > div > div:first-of-type {
  .field-label {
    padding-top: 0;
  }
}

.ant-popover-content {
  .form-field {
    display: block;
    background: transparent;
    border: 0;
    min-width: initial;
    .reset-field {
      display: none;
    }
    &.radio-number {
      .ui-radio label {
        min-width: 32px;
        text-transform: uppercase;
      }
    }
    &:first-of-type {
      .field-option {
        border-top: 0;
      }
    }
    &:last-of-type {
      .field-option {
        border-bottom: 0;
      }
    }
  }
  .field-label {
    padding: 4px 0;
    font-size: 11px;
    color: #5e718a;
  }
  .field-option {
    margin-left: 0;
    padding: 0;
    min-height: 20px;
    background: transparent;
    border-top: 0;
  }
  .ui-input.input-numbers input {
    height: 25px;
  }
  .ui-radio {
    label {
      padding: 0 7px;
      min-height: 25px;
      min-width: 65px;
      line-height: 25px;
      font-size: 10px;
    }
  }
  .ui-select .Select {
    width: 167px;
    font-size: 12px;
  }
  .ui-input {
    margin-right: 8px;
    padding-right: 0 !important;
  }
  .form-field-group > .form-field > .field-option > .ui-input {
    margin-right: 0;
  }
}

.bg-popover {
  .ui-input.input-numbers {
    margin-right: 8px;
    + .ui-radio {
      margin-left: 0;
    }
  }
}

.form-field-group {
  margin-top: 5px;
  display: flex;
  align-items: center;
  .form-field {
    flex-grow: 0;
  }
  .field-option {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .input-numbers input {
    min-width: 75px;
  }
  > div:not(:last-of-type) {
    padding-right: 10px;
  }
}

.form-field + div > .form-field:last-child .field-option {
  border-bottom: 0;
}

.tab-content > div:not(.image-dimensions) .ui-image-dimensions.sub-tabs {
  margin: -10px -10px -10px -10px;
  width: calc(100% + 20px);
}

.form-field.filter {
  .ui-list {
    > div > ul {
      > li:first-of-type {
        margin-top: 0;
      }
      &:empty::before {
        content: "Click + to add item";
        font-family: "Open Sans";
      }
    }
  }
  .list-add-btn {
    background: $blue;
    &:hover {
      background: color($blue shade(10%));
    }
  }
}

.form-component + .form-field {
  border-top-width: 0;
}

.info-modal {
  .ant-modal {
    max-width: 530px;
    top: 45%;
    margin-top: 0;
  }
  .ant-modal-body div {
    padding: 15px;
  }
}

.field-info {
  .field-option {
    padding: 0 10px;
  }
  .reset-field {
    display: none;
  }
  .ui-text, textarea {
    width: 100% !important;
    background: transparent !important;
    border: 0 !important;
    pointer-events: none;
    height: auto !important;
    padding: 0 !important;
  }
  ::-webkit-input-placeholder {
    color: $dark;
    opacity: 1;
    font-size: inherit;
    padding-top: 10px;
  }
  ::-moz-placeholder {
    color: $dark;
    opacity: 1;
    font-size: inherit;
    padding-top: 10px;
  }
  :-ms-input-placeholder {
    color: $dark;
    opacity: 1;
    font-size: inherit;
    padding-top: 10px;
  }
  :-moz-placeholder {
    color: $dark;
    opacity: 1;
    font-size: inherit;
    padding-top: 10px;
  }
}

.form-component .tabs-horizontal .tab-container .form-field:first-of-type + div {
  border-top: 1px solid $ee;
}

.tabs-field {
  > .field-label .reset-field {
    display: none !important;
  }
  .tabs-horizontal {
    margin: -10px;
    flex-grow: 1;

    .field-label {
      //display: none;
    }
    .form-field {
      flex-direction: column;
      align-items: flex-start;
      background: $e2;
      border-top-width: 0 !important;
      &:hover .field-label .reset-field {
        right: -10px;
        display: block;
      }
    }
    .field-label {
      max-width: 100%;
      flex: 0 0 auto;
      padding: 6px 8px;
      line-height: 1;
      font-size: 11px;
      .reset-field {
        display: none;
        right: -10px;
      }
    }
    .accordion-heading {
      background: color($quartz tint(50%));
      border-top-color: $ee;
      border-bottom-width: 0;
      transition: background 0.075s ease-out;
      &:hover {
        background: color($quartz tint(40%));
      }
    }
    .accordion-open .accordion-heading {
      border-bottom: 1px solid $ee;
      background: color($quartz tint(40%));
    }
    .accordion-content > div:not(.form-field) > .form-field:last-child {
      border-bottom-width: 0;
    }
    .tab-container {
      background: transparent;
      .tab-content {
        padding: 0 !important;
        > .ui-list {
          padding: 10px;
          position: relative;
        }
      }
      .form-field:last-child .field-option {
        border-bottom: 0;
      }
      .form-field:first-of-type + div {
        border-top: 1px solid $ee;
      }
      .field-option {
        background: $fa;
        width: 100%;
      }
      .accordion-content .field-option {
        background: white;
      }
    }
    .tab-items {
      background: transparent;
      > li {
        height: 30px;
        font-size: 11px;
        padding: 0 11px;
        background: $ee;
      }
    }
  }
}

.form-component .form-field{
  &::before{
   // content: "";
    position: absolute;
    z-index: 222;
    width: 100%;
    height: calc(100% + 1px);
    background: rgba(0,0,0,.4);
    transition: all 0.075s ease-out;
    pointer-events: none;
  }
  &:hover::before{
    opacity: 0;
  }
}

