@import '../variables.scss';

.ui-items-per-row {
  display: flex;

  .edit-btn {
    margin-left: 8px;
  }
}

.items-per-row-section{
  .ant-input-number-input{
    max-width:32px;
    height: 29px;
    text-align: center;
  }

  .ant-modal{
    max-width: 700px;
  }

  .list-add-btn{
    height: calc(100% - 20px);
    right: 10px;
    &::before{
      position: relative;
    }
  }
  .list-remove-btn{
    height: 28px;
  }
  .ui-input,
  .ui-list-item{
    margin-right:8px;
  }
  .breakpoints{
    margin-left:8px;
  }
  .ui-list {
    min-width: 88px;
    ul:not(:empty){
      //margin-right:35px;
    }
  }
  .ui-list-group {
    display: flex;
    min-width: 126px;
    + div{
      .ui-list-group{
        min-width: 90px;
        .input-number{
          margin-right:0;
        }
      }
    }
  }
  .breakpoints .ui-input.input-numbers input{
    height: 29px;
  }
  .ant-popover-inner-content > div {
    display: flex;
    > div{
      background-color: $e7;
      padding:4px 8px 8px;
    }
    > div:not(:first-of-type){
      margin-left:10px;
    }
  }
}
