@import '../variables.scss';

$bg: color(#6D819A tint(0%));



.ant-modal-wrap{
  //overflow: hidden;
}

.ant-modal-header {
  padding: 10px 12px;
}
.ant-modal-title{
  font-size: 16px;
  display: flex;
  padding-right: 20px;
}


.ant-modal {
  top: 50%;
  //transform: translateY(-50%);
  margin-top:-45vh;
  width: auto !important;
  max-width: 1300px;

  .ant-modal-body {
    padding: 0;
    margin-bottom: 100px;
    min-height: 30px;
    .field-label{
      padding-left:15px;
    }
    .module-items .field-label{
      flex: 0 0 168px;
      padding-left:8px;
    }
    .form-field:first-child .field-option{
      border-top-width: 0;
    }
    .form-field:last-child .field-option{
      border-bottom-width: 0;
    }
  }
  .save-button {
    margin-right: 18px;
    font-size: 19px;
    padding: 3px 6px;
    top: 1px;
    > .icon{
      top: 1px;
    }
  }
  .ant-modal-close {
    width: 31px;
    height: 28px;
    border-radius:3px;
    line-height: 1;
    top: 8px;
    right: 11px;
    background: $orange;
    &:hover {
      background: color($orange tint(15%));
    }
    .ant-modal-close-x {
      font-size: 22px;
      color: white;
      width: auto;
      height: auto;
      line-height: 1;
      font-size: 16px;
      &::before{
        //content: "\e633";
      }
    }
  }
  .j-header {
    display: none;
  }
  .j-content{
    min-width: 100%;
  }
  .tabs-vertical {
    display: flex;
    > ul {
      position: relative;
      height: auto;
      padding-bottom:0;
    }
    .tab-container{
      margin-left:0;
      //padding:0;
      .tab-content{
        min-height: 100%;
      }
    }
  }
  .module-items{
    //height: calc(80vh - 138px);
    //overflow-y: auto;
    &.tabs-vertical {
      > ul {
        height: auto;
        min-width: 170px;
      }
      .tab-container{
        //padding:0 0 0 8px;
        .accordion-group {
          background-color: $fa;
        }
      }
    }
    &::-webkit-scrollbar {
      width: 5px;
    }
    &::-webkit-scrollbar-track {
      background: $c2;
    }
    &::-webkit-scrollbar-thumb {
      background: $blue;
    }
  }
  .tabs-vertical > ul{
    height: calc(100vh - 110px);
  }
  .ui-link .ui-input.input-large input{
    max-width: 200px;
  }
}

.ant-modal-content {
  max-height: 85vh;
  //overflow: hidden;
}


.ant-modal-footer{
  //display: none;
  padding:10px;
  .button{
    background-color: $green;
    width: 30px;
    height: 27px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    margin-left: auto;
  }
}

.ant-modal-header,
.ant-modal-footer{
 background-color: $fa;
}


.page-skins{
  overflow-y: hidden !important;
  .ant-modal-content {
    overflow: visible;
    max-height: 100%;
  }
  .ant-modal-footer {
    display: block;
  }
  .ant-modal-wrap{
    overflow-y: auto;
  }
}

.ant-pagination-item,
.ant-pagination-prev,
.ant-pagination-jump-prev,
.ant-pagination-jump-next{
  margin-right:5px;
}

.oc3 .ant-modal-body .page{
  padding-top: 0;
}

.edit-modal {
  .field-style-name,
  .field-variable-name {
    display: none;
  }
}

.presets-modal .button.save-button {
  padding: 3px;
  margin-left: 20px;
}
