@import '../variables.scss';

$bg: $night;
$bg2: $blue;
$rad: 2px;


.new-feature{
  letter-spacing: 0;
  padding: 2px 5px;
  margin-left: 3px;
  border-radius: 3px;
  background: $orange;
  .item-name &{
    padding:0 3px 1px;
    font-size: 85%;
  }
}

.last-off .ui-radio {
  > span:last-child {
    label.is-checked {
      background: $orange !important;
    }
  }
}

.grid-type .ui-radio {
  span:last-child:nth-child(3),
  span:last-child:nth-child(4) {
    label {
      background: lighten($gray, 10%);
    }
  }
}

.legacy-feature{
  background: transparent;
  border-radius: 0;
  opacity: .6;
  margin-left: 5px;
  font-size: 85%;
}

.ui-radio {
  position: relative;
  display: flex;
  > span {
    text-align: center;
    display: inline-block;
    vertical-align: middle;
    &:first-of-type {
      border-left: 0;
      label {
        border-left: 0;
        border-radius: $rad 0 0 $rad;
      }
    }
    &:last-of-type {
      border-right:0;
      label {
        border-right: 0;
        border-radius: 0 $rad $rad 0;
      }
    }
  }
  input {
    display: none;
    //order: -1;
    //margin-right: 5px;
    //pointer-events: none;
  }
  label {
    color: white;
    cursor: pointer;
    margin-bottom: 0;
    padding: 0 8px;
    min-height: 29px;
    line-height:1;
    position: relative;
    font-size: 11px;
    font-weight: normal;
    background: $bg;
    transition: all 0.05s;
    min-width: 55px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-left:1px solid color($bg tint(20%));
    border-right:1px solid color($bg shade(15%));
    i{
      font-size: 18px;
      //top: 1px;
      position: relative;
      opacity:.5;
    }
    &:hover {
      background: color($bg tint(10%));
    }
  }
  .is-checked{
    background: $bg2 !important;
    box-shadow: inset 1px 3px 10px rgba(0, 0, 0, 0.3);
    color: white;
    border-right:1px solid color($bg2 shade(15%));
    i{
      opacity:.8;
    }
  }
  &.radio-number{
    label{
      min-width: 38px;
      text-transform: uppercase;
    }
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  }
  &.img-dim{
    label{
      min-width: 42px;
    }
  }
}

.arrow-down{
  &::before{
    font-family: 'icomoon-admin';
    content: "\f175";
    font-size: 15px;
  }
}
.radio-toggle{

  label{
    min-width: 33px !important;
    font-size: 15px;
  }

  // inherit/on/off
  > span:nth-last-child(3){
    &:first-child{
      label{
        &.is-checked{
          background: color($blue shade(0%)) !important;
        }
        &::before{
          font-family: 'icomoon-admin';
          content: "\f175";
        }
      }
    }
    &:first-child + span{
      label{
        &.is-checked{
          background: color($green shade(0%)) !important;
          border-color: #178444;
        }
        &::before{
          font-family: 'icomoon-admin';
          content: "\eb1b";
          font-size: 16px;
        }
      }
    }
    &:first-child + span + span{
      label{
        &.is-checked{
          background: color($orange shade(0%)) !important;
        }
        &::before{
          font-family: 'icomoon-admin';
          content: "\e5cd";
          font-size: 16px;
        }
      }
    }
  }
  // on/off
  > span:nth-last-child(2){
    &:first-child{
      label{
        &.is-checked{
          background: color($green shade(0%)) !important;
          border-color: #178444;
        }
        &::before{
          font-family: 'icomoon-admin';
          content: "\eb1b";
          font-size: 16px;
        }
      }
    }
    &:first-child + span{
      label{
        &.is-checked{
          background: color($orange shade(0%)) !important;
        }
        &::before{
          font-family: 'icomoon-admin';
          content: "\e5cd";
          font-size: 16px;
        }
      }
    }
  }
}

.radio-number .ui-radio{
  label{
    min-width: 32px;
    text-transform: uppercase;
  }
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.option-input + .ui-radio{
  margin-left: 15px;
}

.ui-toggle{
  display: inline-flex;
  align-items: center;
  justify-content: center;
  .ant-switch{
    min-width: 38px;
    border: 1px solid color($steel tint(60%));
    background-color: color($steel tint(60%));
    transition: all 0.1s ease-out;
    &:hover{
      border-color: color($steel tint(30%));
      background-color: color($steel tint(30%));
    }
    &:active:after {
      width: 18px;
    }
    &.ant-switch-checked:active{
      &::after{
        margin-left:-19px;
        width: 18px;
      }
    }
    &::after{
      transition: all 0.1s, width 0.1s;
      font-size: 10px;
      color: $c2;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  .ant-switch-checked {
    border-color: $green;
    background-color: $green;
    &:hover{
      border-color: color($green shade(10%));
      background-color: color($green shade(10%));
    }
  }
}

div + .ui-toggle{
  margin-left:8px;
}

.menu-trigger .ui-radio span:last-child{
  .is-checked {
    background: rgb(237, 81, 73) !important;
  }
}

