@import '../variables.scss';

.ui-slider{
  width: 100%;
  width: 250px;
  display: flex;
  align-items: center;
  .slider-lg &{
    max-width: 400px;
  }
}

@media only screen and (max-width: 760px){
  .ui-slider{
    width: 170px;
  }
}

.ant-slider{
  width: 100%;
  margin:0;
  + span{
    display: inline-flex;
    padding:2px 5px 3px 4px;
    color: white;
    background-color: $blue;
    font-size: 90%;
    border-radius:3px;
    margin-left:10px;
    position: relative;
    &::before{
      content: "";
      position: absolute;
      border:5px solid transparent;
      border-right-color: $blue;
      left: 0;
      top: 50%;
      transform: translate(-100%, -50%);
    }
  }
}

.ant-slider-rail{
  background-color: $dd;
}
.ant-slider-handle{
  border-color: $blue;
}
