@import "variables";

.module-items {
  &.tabs-vertical > .tab-items {
    li {
      &:first-of-type {
        //border-top-left-radius: 2px;
        //border-top-right-radius: 2px;
      }
      &:last-of-type {
        //border-top-color: $oc;
        border-bottom: 0;
        //border-top: 0;
        height: 45px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0;
        //border-bottom-left-radius: 2px;
        //border-bottom-right-radius: 2px;
        .new-item{
          width: calc(100% - 14px);
          height: 30px;
          border-radius:2px;
        }
        &:hover {
          background: $night2;
        }
      }
      &:nth-last-of-type(2) {
        //border-bottom: 0;
      }
      &.tab-active {
        .move-handle {
          //background: $orange;
          //color: white;
        }
        &:not(:first-of-type) {
          &::before {
            display: none;
          }
        }
      }
    }
  }
  .is-sortable {
    cursor: pointer;
    .move-handle {
      //background: #3F414B;
      background-color: transparent;
      color: white;
      width: 15px;
      padding-right: 5px;
      height: calc(100%);
      border-right: 0;
      left: 0;
      justify-content: flex-end;
      opacity: .6;
      transform: translateY(1%);
      top: 0;
      &:hover {
        color: $green;
        opacity:1;
      }
    }
    &:hover,
    &.tab-active {
      .move-handle {
        opacity: 1;
        color: $orange;
      }
    }
  }
}


div.new-item {
  pointer-events: auto;
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.1s ease-out;
  border-radius: 0;
  font-size: 13px;
  line-height: 39px;
  background: $blue;
  &:hover {
    background: color($lightmarine shade(0%));
  }
  i {
    font-size: 18px;
    top: 2px;
  }
}
.tab-content > div > .module-items > .accordion-item > .accordion-heading{
  border-top-width: 0;
  border-bottom-width: 0;
}

.transition-name {
  display: inline-block;
  margin-left: 7px;
}

.modal.transitions-modal {
  .modal-body {
    width: 900px;
    height: 349px;
    > div > i {
      position: absolute;
      top: 46%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: 0;
      color: white;
      font-size: 30px;
    }
  }
  .modal-header {
    display: none;
  }
  iframe {
    width: 900px;
    height: 349px;
    position: relative;
    z-index: 1;
  }
}

/******************************
 SLIDER LAYERS
*******************************/

$bg: color(#798FAB shade(10%));

.subitems {
  position: relative;
  > ul {
    position: relative;
    border: 0;
    margin: 0;
    padding: 0;
    list-style: none;
  }
  .fa-cog {
    top: 1px;
    position: relative;
  }
}

.subitem-buttons {
  background: color($e2 shade(0%));;
  height: 45px;
  display: flex;
  align-items: center;
  span {
    background: color($dd shade(0%));
    color: $dark;
    padding: 0 15px;
    height: 100%;
    display: flex;
    align-items: center;
    margin-right: 10px;
    min-width: 180px;
  }
  .button {
    margin-right: 8px;
    padding: 4px 10px;
    border-radius: 3px;
    color: white;
    background: $blue;
    &:hover{
      background: color($blue shade(15%));
      color: white;
    }
    &.add-image-layer{
      background: $green;
      &:hover{
        background: color($green shade(15%));
      }
    }
    &.add-video-layer{
      background: $red;
      &:hover{
        background: color($red shade(15%));
      }
    }
    &.add-button-layer{
      background: $yellow;
      &:hover{
        background: color($yellow shade(15%));
      }
    }
    &.add-product-layer{
      background: $purple;
      &:hover{
        background: color($purple shade(15%));
      }
    }
    i {
      margin-right: 5px;
      color: white;
      font-size: 14px;
      top: 1px;
    }
  }
}

li.subitem {
  position: relative;
  background: $bg;
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  padding-left: 40px;
  border-top: 1px solid color($bg tint(15%));
  border-bottom: 1px solid color($bg shade(15%));
  transition: all 0.05s ease-out;
  &:hover{
    background: color($bg shade(5%));
    > div > a{
      color: white;
      opacity:1;
    }
  }
  > div{
    width: 100%;
    height: 40px;
    display: flex;
    align-items: center;
    > a{
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      padding-left:10px;
      opacity:.7;
      cursor: pointer;
      color: white;
    }
  }
  .module-icons a {
    color: $dd;
    cursor: pointer;
    padding:5px;
    &:active{
      box-shadow: none;
    }
    > span{
      display: inline-block;
      margin:0 3px;
    }
    &:hover {
      color: white !important;
    }
  }
  &::before {
    content: "";
    color: $dark;
    size: 39px;
    position: absolute;
    font-size: 16px;
    left: 0;
    border-radius:0;
    display: flex;
    align-items: center;
    justify-content: center;
    background: $quartz;
  }
  &:first-of-type {
    border-top-color: color($bg tint(5%));
  }
  &:last-of-type {
    border-bottom-color: color($bg tint(5%));
  }
  &.text-layer::before {
    content: "\f031";
    color:color($blue tint(10%));
  }
  &.image-layer::before {
    content: "\f030";
    color: $green;
  }
  &.video-layer::before {
    content: "\f03d";
    color: color($red tint(10%));
  }
  &.button-layer::before {
    content: "\f0a6";
    color: $yellow;
  }
  &.product-layer::before {
    content: "\f217";
    color: color($purple tint(10%));
  }
}

#container .side-column .flyout img{
  max-width:500px;
}


.variable-color,
.variable-gradient,
.variable-shadow {
  .variable-value {
    display: inline-block;
    width: 15px;
    height: 15px;
  }
}

.variable-shadow .variable-value{
  display: none !important;
}
