@import "variables";

.j-logo {
  position: relative;
  width: $lw;
  min-width: 170px;
  justify-content: center;
  background: $blue;
  img{
    position: relative;
    left: -4px;
  }
  > span {
    letter-spacing: 0;
    font-size: 10px;
    font-family: $sans;
    text-transform: none;
    font-weight: normal;
    display: block;
    padding: 2px 5px;
    border-radius: 10px;
    background: $orange;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(50%, -50%);
  }
}

.j-title {
  padding: 0 20px 0 27px;
  font-size: 15px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  div{
    display: flex;
    align-items: center;
    small{
      font-size: 80%;
      opacity: .7;
      margin-left: 8px;
    }
  }
}

.j-middle {
  flex: 1 0 auto;
  padding: 0 30px 0 0;
  display: none;
  .ui-select + .ui-select {
    margin-left: 7px;
  }
}

.j-buttons {
  margin-left: auto;
  transition: all 0.3s;
}

.ant-modal .button.save-button{
  background: $green;
  width: auto;
  padding: 7px;
  font-size: 11.7px;
  text-transform: uppercase;
  font-weight: 500;
  top: 0;
  .icon-admin{
    font-size: 16px;
    margin-right: 3px;
  }
  &:hover{
    background: #219452;
  }
}

.ant-modal .button.save-button .icon-admin {
  font-size: 16px;
  margin-right: 0;
  top: 2px;
}

.j-header {
  //transition: all 0.3s;
  padding-right: 4px;
  display: flex;
  background: $night;
  height: 45px;
  font-family: $sans;
  color: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  > div {
    height: 100%;
    display: flex;
    align-items: center;
  }
  .button {
    width: 30px;
    height: 27px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 13px;
    margin: 0 4px;
    padding: 0;
    position: relative;
    i {
      font-size: 18px;
      position: relative;
      top: -1px;
    }
    &.save-button {
      background: $green;
      width: auto;
      padding: 0 7px;
      font-size: 90%;
      text-transform: uppercase;
      font-weight: 500;
      .icon-admin{
        font-size: 16px;
        margin-right: 3px;
      }
      .loader {
        size: 14px;
      }
      .icon{
        &::before{
          content: "\eb1b";
          font-family: 'icomoon-admin';
        }
      }
      &:hover {
        background: color($green shade(15%));
      }
    }
    &.reset-button {
      background: $yellow;
      .loader {
        size: 14px;
      }
      &:hover {
        background: color($yellow shade(15%));
      }
    }
    &.stores-button {
      background: $yellow;
      i {
        position: relative;
        top: -1px;
      }
      &:hover {
        background: color($yellow shade(20%));
      }
    }
    &.back-button {
      background: $orange;
      &:hover {
        background: color($orange shade(15%));
      }
      i {
        font-size: 19px;
      }
    }
    &.new-button {
      background: color($blue tint(0%));
      width: auto;
      padding: 0 7px;
      font-size: 90%;
      text-transform: uppercase;
      font-weight: 500;
      .icon-admin{
        font-size: 16px;
        margin-right: 3px;
      }
      i {
        font-size: 22px;
      }
      &:hover {
        background: color($blue shade(15%));
      }
    }
    &.fs-button {
      display: none;
      background: $lightmarine;
      i {
        font-size: 22px;
      }
      &:hover {
        background: color($lightmarine shade(10%));
      }
    }
    &.clear-cache-button {
      background: $green2;
      i {
        font-size: 18px;
      }
      &:hover {
        background: color($green2 shade(10%));
      }
    }
    &.remove-selected-button {
      background: $orange;
      &:hover {
        background: color($orange shade(10%));
      }
    }
    &.disabled {
      opacity: .3;
      pointer-events: none;
    }
  }
}

/******************************
 Prevent Logout
*******************************/

.oc2 #content::before {
  content: "";
  width: 88px;
  height: 15px;
  position: absolute;
  z-index: 9999;
  right: 0;
  top: 30px;
}

@media only screen and (max-width: 1200px) {
  #content::before {
    width: 45px;
  }
}

@media only screen and (min-width: 760px) {
  #header {
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 12;
  }
  .j-header {
    position: fixed;
    top: 45px;
    width: calc(100% - 50px);
    z-index: 10;
  }

  #column-left.active + #content .j-header {
    .j-buttons {
      margin-right: 185px;
    }
  }
  #column-left {
    position: fixed;
    z-index: 11;
  }
  .page {
    padding-top: 90px;
  }
  .page-layout .page {
    padding-top: 0;
  }
}

/******************************
 Loader
*******************************/

.loading {
  margin:8px;
  .fa-spin {
    top: 2px;
    position: relative;
    font-size: 18px;
    margin-right: 6px;
  }
}

.page .button .fa-spin {
  transform-origin: 50% 53%;
  min-width: 10px;
  min-height: 10px;
  &::before{
    font-size: 18px;
    top: 1px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.ant-modal{
  .save-button{
    .fa-spin {
      min-width: 19px;
      min-height: 15px;
    }
  }
}

/******************************
 Fullscreen
*******************************/

.fs-layout {
  .icon-fullscreen:before {
    content: "\e5d1";
  }

  overflow-y: hidden !important;

  .tabs-vertical > .tab-container > .tab-content {
    min-height: calc(100vh - 63px);
  }

  * {
    transition: all 0s !important;
  }

  .tabs-vertical > ul {
    height: 100vh;
  }

  #content {
    position: fixed;
    width: 100%;
    height: 100%;
    //left: -50px; for OC 2
    margin-left: 0 !important;
    top: 0;
    z-index: 100;
    background: white;
    &.page-layout {
      background: $e2;
    }
    &::before {
      display: none;
    }
    .j-header {
      top: 0;
      width: 100%;
    }
    .j-content {
      max-width: 100%;
      height: 100%;
      padding: 0;
      > .tabs {
        min-height: calc(100vh - 45px);
      }
      > .tabs-vertical > ul {
        border-left: 0;
        height: calc(100vh - 45px);
      }
    }
    .position-selectors {
      top: 45px;
      width: 100%;
    }
    .page {
      height: 100%;
      overflow-y: scroll;
      padding-top: 45px;
    }
    .positions {
      margin-top: 40px;
    }
  }
  #column-left.active + #content {
    left: 0;
    .j-header .j-buttons {
      //margin-right:0;
      right: -185px;
      position: absolute;
    }
    .position-selectors {
      width: 100%;
    }
  }
}

.oc3 {
  #content {
    transition: all 0s;
  }
  body:not(.fs-layout){
    #content{
      //z-index:5; not working with ck editor modal
    }
  }
  #header {
    box-shadow: 0 0 5px rgba(0, 0, 0, .4);
    border-bottom-width: 0;
  }
  .page {
    padding-top: 107px;
    //overflow-x: auto;
    //overflow-x:auto; select 2 not visible
  }
  .j-header {
    width: calc(100% - 235px);
    height: 47px;
    top: 60px;
  }
  #footer {
    margin-left: 405px !important;
    transition: all 0s;
    z-index:4;
    z-index: 0;
  }
  .page-layout,
  .page-layouts,
  .page-skins,
  .page-footer,
  .page-dashboard,
  .page-module_product,
  .page-module_layout,
  .page-import_export,
  .page-blog_posts{
    #footer {
      margin-left: 235px !important;
    }
  }
  .tabs-vertical > ul {
    //height: calc(100vh - 107px);
    padding-bottom:0;
  }
  .fs-layout {
    overflow-y: scroll !important;
    #content {
      .position-selectors {
        top: 47px;
      }
      .j-content > .tabs {
        min-height: calc(100vh - 47px);
      }
      .page {
        padding-top: 47px;
        overflow-y: hidden;
      }
    }
    .page-module,
    .page-modules {
      + #footer {
        margin-left: 170px !important;
      }
    }
    #footer {
      display: none;
    }
  }

  .j-header,
  .position-selectors{

  }
}

.oc4 {
  .page {
    padding-top: 113px;
  }

  .j-header {
    top: 67px;
  }
}

@media only screen and (max-width: 767px){
  .oc3 {
    .page {
      padding-top: 0;
    }

    .j-header {
      width: calc(100%);
      top: 45px;
    }
  }
}

