@import '../variables.scss';

$col: $dd;
$hov: $lightblue;

.ui-icon-editor{
  display: flex;
  align-items: center;
  .ui-color{
    margin-right:8px;
  }
  .ui-checkbox{
    margin-left: 8px;
  }
  .ui-input.input-number input{
    min-width: 50px;
  }
}

.ui-icon-preview{
  width: $w;
  height: $w - 3;
  margin-right:8px;
  position: relative;
  z-index:1;
  border-radius: 2px;
  border: 1px solid $col;
  cursor:pointer;
  transition: all 0.05s ease-out;
  background: white;
  &::before{
    transition: all 0.05s ease-out;
    display: flex;
    align-items: center;
    justify-content: center;
    content: "\F05E";
    font-family: 'icomoon-admin';
    font-size: 20px;
    color: $dd;
  }
  &:hover{
    background: $alice;
  }
  &.has-icon{
    &::before{
      display: none;
    }
    > i{
      color: $quartz;
      font-size: 20px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}

.icon[style="color: rgb(255, 255, 255);"]{
  &::before{
    color:$e4 !important;
  }
}

/******************************
POPOVER
*******************************/

$bg: white;
$hov: $quartz;
$col: $f4;

div.icon-popover{
  .ant-popover-inner{
    background: $col;
  }
  .pt-popover-arrow{
    transform: translateY(5px);
  }
  .pt-popover-content{
    background: $col;
    padding-bottom:10px;
    max-width:100%;
  }
  .pt-popover-arrow-fill{
    fill: $col;
  }
  .ui-input.input-large, .ui-input.input-large input{
    width: 100%;
    margin-bottom:5px;
  }
  .ReactVirtualized__Grid {
    background: $bg;
    overflow-x: hidden !important;
    border: 1px solid $dd;
    &:focus{
      outline: none;
      box-shadow: none;
    }
  }
  .icon-cell{
    display: flex;
    align-items: center;
    justify-content: center;
    border-right:1px solid $dd;
    border-bottom:1px solid $dd;
    font-size: 22px;
    color: $hov;
    &:hover, &.icon-selected{
      background: $blue;
      color: white;
      cursor: pointer;
    }
  }
  .ant-popover-content > .ant-popover-arrow{
    border-top-color: $col;
    //border-bottom-color: $col;
    box-shadow: none;
    background-color: transparent;
    &::after{
      border-top-color:$col;
      border-bottom-color: $col;
    }
  }
}

.icon-popover.ant-popover-placement-bottom > .ant-popover-content > .ant-popover-arrow{
  top: -7px;
  //margin-left: -8px;
  transform: translateX(-50%) rotate(180deg);
}

.ant-popover{
  ::-webkit-input-placeholder {
    color: $aa;
    font-size: 12px;
  }
  ::-moz-placeholder {
    color: $aa;
    font-size: 12px;
  }
  :-ms-input-placeholder {
    color: $aa;
    font-size: 12px;
  }
}

.icon-popover .ant-popover-content{
  .ant-popover-arrow{
    border-top-color: transparent !important;
  }
  .ui-radio label{
    min-width: 52px;
  }
  .ui-margin .ui-input:first-child{
    display: none;
  }
}

.ui-icon-editor .ui-checkbox {
  margin-left: 8px;
}















