@import '../variables.scss';

$w: 75px;

.ui-image {
  display: inline-block;
  position: relative;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  cursor: pointer;
  .button {
    padding: 0;
    &:active {
      box-shadow: none;
    }
  }
  i {
    cursor: pointer;
    font-size: 60px;
    color: $dd;
    position: relative;
    top: 1px;
    &:hover {
      opacity: .7;
    }
  }
  .ui-clear{
    i{
      font-size: 12px;
      top: 0;
    }
  }
  &.has-image {
    .ui-image-preview {
      //border: 1px solid $dd;
      width: auto;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      max-width: 400px;
      min-width: 63px;
      min-height: 60px;
      img {
        max-width: 100%;
        max-height: 150px;
        height: auto;
      }
    }
  }
}

.ui-image-lang{
  .ui-image{
    margin-left: 10px;
  }
}
.ant-modal .ui-image-lang{
  .ui-image{
    margin-left: 0;
  }
}
