@import '../variables.scss';

$time: 100ms;
$col: black;
$dis: 8px;


/******************************
 TOOLTIP
*******************************/

.ant-tooltip{
  z-index:3000;
  transition: all 0s !important;
  *{
    transition: all 0s !important;
  }
  &.popup-tip{
    z-index:6000;
  }
}

.ant-tooltip-inner{
  background: $black;
  min-height:100%;
  padding: 3px 7px;
  border-radius: 2px;
  font-size: 10px;
  line-height: 1.3;
}
.ant-tooltip-placement-top,
.ant-tooltip-placement-topLeft,
.ant-tooltip-placement-topRight {
  .ant-tooltip-arrow {
    border-top-color: $black;
    transform: translateY(7px);
  }
  .ant-tooltip-content{
    transform: translateY(7px);
  }
}
.ant-tooltip-placement-bottom,
.ant-tooltip-placement-bottomLeft,
.ant-tooltip-placement-bottomRight {
  .ant-tooltip-arrow {
    border-bottom-color: $black;
    transform: translateY(-8px);
  }
  .ant-tooltip-content{
    transform: translateY(-5px);
  }
}

.ant-tooltip-placement-left .ant-tooltip-arrow {
  margin-top: 0;
  transform: translateY(-47%);
  border-left-color: $black;
}

.ant-popover-placement-left > .ant-popover-content > .ant-popover-arrow,
.ant-popover-placement-leftTop > .ant-popover-content > .ant-popover-arrow,
.ant-popover-placement-leftBottom > .ant-popover-content > .ant-popover-arrow{
  border-left-color: $f7;
}

.ant-tooltip-placement-bottomRight .ant-tooltip-arrow {
  right: 10px;
}



/******************************
 POPOVER
*******************************/

$bg: $f6;
$pad: 8px;

.ant-popover{
  z-index: 2999;
}

.ant-popover-inner {
  border-radius: 3px;
  background: $bg;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.25);
}

.ant-popover-inner-content{
  padding: $pad;
  .gradient-popover &{
    padding: $pad $pad $pad - 5 $pad;
  }
  .info-popover &{
    padding: $pad + 2 $pad + 4;
  }
}

.info-popover{
  max-width: 260px;
  .ant-popover-inner {
    background: white;
  }
  .ant-popover-inner-content{
    padding: 10px;
    img{
      max-width: 400px;
    }
  }
  &.ant-popover-placement-bottom{
    > .ant-popover-content > .ant-popover-arrow{
      border-bottom-color: white;
    }
  }
}

.ant-popover-arrow {
  border-width: 8px;
}

//RIGHT
.ant-popover-placement-right,
.ant-popover-placement-rightTop,
.ant-popover-placement-rightBottom{
  padding-left:4px;
}

.ant-popover-placement-right > .ant-popover-content > .ant-popover-arrow,
.ant-popover-placement-rightTop > .ant-popover-content > .ant-popover-arrow,
.ant-popover-placement-rightBottom > .ant-popover-content > .ant-popover-arrow{
  left: -11px;
  border-right-color: $bg;
  margin-top:0;
  transform: translateY(-50%);
  background-color: transparent !important;
  &::after{
    border-right-color: $bg;
  }
}

//TOP
.ant-popover-placement-top > .ant-popover-content > .ant-popover-arrow,
.ant-popover-placement-topLeft > .ant-popover-content > .ant-popover-arrow,
.ant-popover-placement-topRight > .ant-popover-content > .ant-popover-arrow{
  border-top-color: white;
  bottom: -7px;
  &::after{
    border-top-color: white;
  }
}
.ant-popover-placement-top > .ant-popover-content > .ant-popover-arrow{
  margin-left:0;
  transform: translateX(-50%);
}

.border-popover .ant-popover-inner-content{
  min-width: 287px;
}

.ant-popover-placement-right > .ant-popover-content > .ant-popover-arrow, .ant-popover-placement-rightTop > .ant-popover-content > .ant-popover-arrow, .ant-popover-placement-rightBottom > .ant-popover-content > .ant-popover-arrow{
  box-shadow: none;
}

