@import '../variables.scss';

/******************************
 Tabs
*******************************/

.is-sortable {
  cursor: -webkit-grab;
  cursor: grab;
  &.gu-mirror {
    cursor: -webkit-grabbing;
    cursor: grabbing;
    border: 0;
    opacity: .8;
    &.tab-active {
      border: 0;
    }
  }
  &.gu-transit {
    box-shadow: inset 0 0 12px rgba(0, 0, 0, .6);
    border: 0;
    + li {
      border-top-color: $quartz;
    }
  }
}

/******************************
 Layout Manager
*******************************/

.move-handle {
  cursor: move;
  position: absolute;
  z-index: 1;
  font-size: 14px;
  color: $steel;
  display: flex;
  align-items: center;
  justify-content: center;
}

.move-item {
  position: relative;
}

.is-dragging {
  box-shadow: inset 2px 2px 5px rgba(0, 0, 0, .5);
  background: $night !important;
  &::before {
    content: "\f019";
    text-align: center;
    width: calc(100% - 14px);
    height: calc(100% - 13px);
    left: 7px;
    top:50%;
    transform: translateY(-50%);
    font-family: $fs;
    opacity: .3;
    color: $dd;
    text-transform: uppercase;
    font-size: 20px;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  > * {
    visibility: hidden;
  }
}
