@import '../variables.scss';

$hov: $lightblue;

.shadow-preview{
  width: 33px;
  height: 30px;
  position: absolute;
  border-radius: 2px;
  top: 0;
  left: 0;
  cursor: pointer;
  background: white;
  border: 1px solid $dd;
  &::before{
    content: "G";
    font-family: $sans;
    font-weight:700;
    font-size: 19px;
    color: $dd;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }
  &:hover{
    background: $alice;
  }
}

