@import '../variables.scss';

.ui-select {
  display: flex;
  .edit-btn {
    margin-left: 10px;
  }
}

.ui-select .Select {
  width: 250px;
}
.small-select{
  .Select {
    width: 114px !important;
  }
  .Select--single > .Select-control .Select-value{
    padding-left:7px;
  }
}

.mini-select{
  .Select {
    width: 62px !important;
  }
}

.filter{
  .Select{
    width: 250px;
  }
}

.ui-link .ui-search .Select {
  width: 330px;
}

.ui-link .ui-input.input-large{
  flex-grow: 1;
  input {
    width: 100%;
    max-width: none !important;
  }
}

.ui-list-item {
  //flex-grow: 1; interferes with remove breakpoint on inputnumbers
  max-width: calc(100% - 80px);
}
.ui-link{
  flex-grow: 1;
  .edit-btn{
    min-width: 30px;
  }
  .ui-select.ui-search{
    width: 100%;
    > .Select{
      width: 100%;
    }
  }
}

$bg: white;
$col: $alice;

.Select-control {
  color: $dark;
  background-color: $bg !important;
  border-radius: 2px;
  display: table;
  outline: 0;
  overflow: visible;
  width: 100%;
  height: 30px;
  box-shadow: none;
  border: 1px solid $dd;
  cursor: pointer;
  .ui-clear.ui-edit{
    display: flex;
  }
  &:hover{
    box-shadow: none;
    background: $col !important;
    .Select-arrow-zone{
      background: color($bg shade(30%));
      border-color: $col;
    }
  }
}

.breakpoints{
  .mini-select .select-edit-value .select-edit-label {
    width: 100%;
    text-align: center;
  }
}

.Select-placeholder {
  bottom: 0;
  color: inherit !important;
  line-height: 28px;
  padding-left: 8px;
  padding-right: 8px;
  right: 0;
  cursor: text;
  &:enter{
    outline: 0;
  }
}

.ui-value .Select-placeholder{
  font-size: 12px;
  text-align: center;
}


.Select-input {
  height: 28px;
  > input {
    height: 100%;
    padding: 0;
  }
}

.Select-arrow-zone {
  cursor: pointer;
  display: table-cell;
  text-align: center;
  width: 28px;
  padding-right: 5px;
  background: color($bg shade(25%));
  border: 3px solid $bg;
  border-radius: 3px;
  &::before{
    font-family: $fs;
    color: white;
    font-size: 13px;
    position: relative;
    left: 2px;
    top: 1px;
    content: "\f107";
  }
  &:hover{
    background: color($bg shade(30%));
  }
}

.Select-arrow {
  border-width:0;
  display: none;
}

.is-focused.is-open .Select-control{
  border-radius: 2px;
  background: $col !important;
  border:1px solid $sky;
  .Select-arrow-zone{
    border-color: $col;
  }
}

.is-focused:not(.is-open)>.Select-control{
  outline: 0;
  box-shadow: none;
  border: 1px solid $dd;
}

.VirtualizedSelectFocusedOption {
  background-color: $col;
}

.Select-menu-outer{
  z-index: 99;
  margin-top: 1px;
  background: $bg;
  border-radius: 2px;
  color:$deep;
  border: 0;
  box-shadow: 0 5px 30px rgba(0,0,0,.2);
  font-size: 12px;
}

.form-field[data-name="headerType"] .VirtualSelectGrid {
  min-height: 246px;
}

.Select-option {
  color: $deep;
  padding: 8px 10px;
}

.Select-option.is-focused {
  background-color: $col;
  color: $dark;
}

:not(.Select--multi) > .Select-control .Select-value {
  line-height: 28px;
  font-size: 12px;
  padding:0 7px;
}

.Select-placeholder,
.Select--single > .Select-control .Select-value {
  max-width: 93%;
}

.has-value.is-clearable.Select--single > .Select-control .Select-value {
  padding-right: 10px;
}

.Select.has-value.is-clearable.Select--single > .Select-control .Select-value {
  padding-right: 20px;
}


.Select--multi .Select-value {
  margin-left: 2px;
  margin-top: 2px;
  min-height: 24px;
}

.select-edit-value {
  display: flex;
  .select-edit-label{
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-right: 20px;
    position: relative;
  }
  .select-edit-button {
    a{
      background: $blue;
      position: absolute;
      left: auto;
      right: 17px;
      top: 50%;
      transform: translateY(-50%);
      border-radius: 30px;
      width: 14px;
      height: 14px;
      box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.1);
      cursor: pointer;
      &:hover{
        background: $quartz;
      }
    }
    i{
      color: white;
      &::before{
        font-size: 10px;
      }
    }
  }
}
.ui-font .select-edit-value .select-edit-button a{
  right: 15px;
}
.mini-select{
  :not(.Select--multi) > .Select-control .Select-value{
    padding: 0 6px;
  }
  .select-edit-value .select-edit-label{
    padding-right: 5px;
  }
  .select-edit-value .select-edit-button a{
    right: 0;
    left: auto;
  }
}

.ui-clear.ui-edit{
  //left: 2px;
  transform: translate(-50%, -50%) scale(0.8);
  background: $blue;
  &:hover{
    background: $quartz;
  }
}

.ui-color{
  .ui-clear.ui-edit{
    opacity: 0;
  }
  &:hover{
    .ui-clear.ui-edit{
      opacity: 1;
    }
  }
}

.ui-value.mini-select{
  .Select-menu-outer{
    min-width: 100px;
  }
  .VirtualizedSelectOption + .VirtualizedSelectOption{
    border-top: 1px solid $dd;
  }
  .select-option-value{
    margin-top: 5px;
  }
  .select-option-label{
    font-size: 8px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    position: relative;
    margin-top: -2px;
  }
}

.VirtualizedSelectOption,
.VirtualizedSelectFocusedOption{
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: flex;
  align-items: center;
  font-size: 12px;
  > div{
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

.font-fam {
  .VirtualizedSelectOption,
  .VirtualizedSelectFocusedOption {
    font-size: 13px;
  }
}
.font-sub + .font-fam {
  .VirtualizedSelectOption,
  .VirtualizedSelectFocusedOption {
    font-size: 16px;
  }
}

.ui-font .mini-select .Select {
  width: 50px !important;
  .select-edit-value {
    justify-content: center;
    font-size: 12px;
  }
  .select-edit-label{
    padding-right: 8px;
  }
  .select-edit-button a {
    right: 2px;
  }
}

.select-edit-value{
  //justify-content: center;
}
